import React from "react";
import { withRouter } from "react-router";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import classNames from "classnames";

import NotificationCard from "../dobyComponents/NotificationCard";
import compose from "../../compose";
import getSharedStyles from "../../style";
import { withLocalization } from "../shared/localization";
import withMail from "../../consumers/withMail";
import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import PageHeader from "../dobyComponents/PageHeader";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    title: {
        "font-size": "x-large",
    },
});

const filters = [];
const sort = { field: "created", dir: "desc" };
const page = { limit: 100, skip: 0 };

class Notifications extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            notifications: [],
        };
    }

    async componentDidMount() {
        const { getMails, readMail } = this.props.mail;

        await readMail("");
        await getMails(filters, sort, page);

        const { mails } = this.props.mail;

        this.setState((state) => ({
            notifications: mails.data,
        }));
        this.getSendersData();
    }

    getSendersData = async () => {
        const { notifications } = this.state;
        const { getUserCached } = this.props.user;

        notifications.map(async (n) => {
            const senderData = await getUserCached(n.senderRef.id);
            if (senderData && senderData.avatar) this.setAvatarToNotificationCallback(senderData.avatar, n._id);
        });
    };

    setAvatarToNotificationCallback = (avatar, id) => {
        this.setState((state) => ({
            notifications: state.notifications.map((n) => {
                if (n._id === id) return { ...n, avatar };
                return n;
            }),
        }));
    };

    renderNotifications = () => {
        const { classes } = this.props;
        return this.state.notifications.map((n) => {
            return n.ru && n.ru !== "" ? (
                <Grid item xs={12}>
                    <NotificationCard
                        className={classNames(classes.mt3)}
                        avatar={n.avatar}
                        onClick={() => this.showNotification(n.ru)}
                        subject={n.subject}
                        created={n.created}
                    />
                </Grid>
            ) : (
                <></>
            );
        });
    };

    showNotification = (ru) => {
        if (ru && ru !== "") this.props.history.push(ru);
    };
    goBack = () => this.props.history.goBack();

    render() {
        const { classes, translate } = this.props;
        return (
            <>
                <PageHeader title={this.props.translate('notifications')}/>

                <div className={classNames(classes.container)}>
                    <Grid container justify="center">
                        <Grid item xs={12} sm={10} md={8} lg={6}>
                            
                        <Grid container direction="row" >
                            {this.renderNotifications()}
                        </Grid>

                        </Grid>
                    </Grid>
                </div>
            </>
        );
    }
}

Notifications.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(withStyles(styles), withDoby, withMail, withStore, withLocalization, withRouter, withUser)(Notifications);
