import React, { useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../../style";
import classNames from "classnames";
import { withLocalization } from "../../shared/localization";
import { withRouter } from "react-router";
import compose from "../../../compose";
import MenuIcon from "../icons/HamburgerMenu";
import BackpackIcon from "../icons/Backpack";
import NotificationIcon from "../icons/Notification";
import ProfileIcon from "../icons/Profile";
import config from "../../../config";
import permissions from "../../../permissions";
import { Transition } from "react-transition-group";
import { Link } from "react-router-dom";

import HomeIcon from "../icons/Home";
import HomeFilledIcon from "../icons/HomeFilled";
import UserIcon from "../icons/User";
import UserFilledIcon from "../icons/UserFilled";
import MedalIcon from "../icons/Medal";
import MedalFilledIcon from "../icons/MedalFilled";
import MoreDotsIcon from "../icons/MoreDots";
import ShirtIcon from "../icons/TShirt";
import ShirtFilledIcon from "../icons/TShirtFilled";
import PlusIcon from "../icons/Plus";
import LogoutIcon from "../icons/Logout";
import SettingsIcon from "../../dobyComponents/icons/Settings";
import SelectField from "../form/SelectField";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    navbar: {
        height: "60px",
        width: "100%",
        position: "fixed",
        top: "0",
        left: "0",
        zIndex: "1100",
        background: "white",
        boxShadow: "0px 0px 17px 1px rgba(0,0,0,0.08)",
        [theme.breakpoints.up("lg")]: {
            height: "70px",
        },
    },
    navbarLogged: {
        [theme.breakpoints.up("lg")]: {
            height: "90px",
        },
    },
    navbarDark: {
        [theme.breakpoints.up("lg")]: {
            boxShadow: "none",
            background: theme.palette.secondary.main,
        },
    },
    menuIconHolder: {
        background: theme.palette.primary.main,
        height: "60px",
        width: "60px",
    },
    adminMenuIconHolder: {
        background: "#33d7f5",
        height: "70px",
        width: "70px",
        position: "absolute",
        top: 0,
        left: 0,
    },
    logoImg: {
        height: "20px",
        width: "auto",
    },
    "@media screen and (max-width: 350px)": {
        logoImg: {
            height: "17px",
            width: "auto",
        },
    },
    iconCount: {
        position: "absolute",
        color: "white",
        background: theme.palette.primary.main,
        borderRadius: "20px",
        padding: "2px 5px",
        fontSize: "12px",
        minWidth: "19px",
        textAlign: "center",
        top: "-3px",
        right: "-2px",
    },
    navItemDesktopActive: {
        position: "relative",
        boxShadow: `inset 0 -4px 0 ${theme.palette.primary.main}`,
    },
    dropMenu: {
        position: "absolute",
        zIndex: "1200",
        minWidth: "150px",
        background: theme.palette.secondary.main,
        top: "calc(100% + 2px)",
        left: "0",
        transition: `opacity 150ms ease-in-out`,
        opacity: "0",
    },
    dropMenuItem: {
        fontFamily: "'IBM Plex Mono', monospace",
        fontSize: "12px",
        color: "white",
        padding: "13px 15px",
    },
    dropMenuSeparator: {
        width: "100%",
        border: "0",
        borderTop: "1px solid rgba(0,0,0,0.2)",
        margin: "0",
    },
});

function Navbar(props) {
    const { classes, notificationItems, cartItems, user, translate } = props;
    const [menuOpen, setMenuOpen] = useState(false);

    const closeMenu = () => {
        setMenuOpen(false);
    };

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const transitionStyles = {
        entering: { opacity: 1 },
        entered: { opacity: 1 },
        exiting: { opacity: 0 },
        exited: { opacity: 0 },
    };

    const location = props.history.location;

    let dark = !location.pathname.includes("/eshop") && !location.pathname.includes("/landing");
    const isAdmin = user && (user.permissions & permissions.doby) > 0;

    return (
        <>
            <div className={classNames(classes.navbar, classes.w100, user && classes.navbarLogged, dark && classes.navbarDark)}>
                <div className={classNames(classes.container, classes.h100, classes.p0, classes.pxLg5)}>
                    <div
                        className={classNames(
                            classes.dFlex,
                            classes.w100,
                            classes.h100,
                            classes.pr3,
                            classes.prLg0,
                            classes.justifyContentLgBetween
                        )}>
                        {isAdmin && (
                            <div
                                onClick={props.handleSidebarOpen}
                                className={classNames(
                                    classes.adminMenuIconHolder,
                                    classes.dNone,
                                    classes.dLgFlex,
                                    classes.alignItemsCenter,
                                    classes.justifyContentCenter
                                )}>
                                <MenuIcon className={classNames(classes.textWhite)} size={33} />
                            </div>
                        )}
                        {props.navigationControls && (
                            <div
                                onClick={props.handleSidebarOpen}
                                className={classNames(
                                    classes.menuIconHolder,
                                    classes.dFlex,
                                    classes.dLgNone,
                                    classes.alignItemsCenter,
                                    classes.justifyContentCenter
                                )}>
                                <MenuIcon className={classNames(classes.textWhite)} size={33} />
                            </div>
                        )}
                        <div
                            className={classNames(classes.logoHolder, classes.dFlex, classes.alignItemsCenter, classes.ml4, classes.mlLg0)}
                            onClick={() => props.history.push("/landing")}>
                            <img
                                src={`/img/${config.locale}/logo.png`}
                                className={classNames(classes.logoImg, dark && classes.dLgNone)}
                                alt={"logo"}
                            />
                            {dark && (
                                <img
                                    src={`/img/${config.locale}/logo-light.png`}
                                    className={classNames(classes.logoImg, classes.dNone, classes.dLgBlock)}
                                    alt={"logo"}
                                />
                            )}
                        </div>

                        {user && (
                            <div className={classNames(classes.dNone, classes.dLgFlex, classes.alignItemsCenter)}>
                                <Link
                                    className={classNames(
                                        classes.dFlex,
                                        classes.flexCol,
                                        classes.alignItemsCenter,
                                        classes.justifyContentCenter,
                                        classes.mx4,
                                        classes.h100,
                                        classes.basicLink,
                                        location.pathname.includes("/landing") && classes.navItemDesktopActive
                                    )}
                                    to={"/landing"}>
                                    {location.pathname.includes("/landing") && classes.navItemDesktopActive ? (
                                        <HomeFilledIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    ) : (
                                        <HomeIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    )}
                                    <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>
                                        {translate("navigation.home")}
                                    </div>
                                </Link>
                                <Link
                                    className={classNames(
                                        classes.dFlex,
                                        classes.flexCol,
                                        classes.alignItemsCenter,
                                        classes.justifyContentCenter,
                                        classes.mx4,
                                        classes.h100,
                                        classes.basicLink,
                                        location.pathname.includes("/challenges") && classes.navItemDesktopActive
                                    )}
                                    to={"/challenges"}>
                                    {location.pathname.includes("/challenges") && classes.navItemDesktopActive ? (
                                        <MedalFilledIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    ) : (
                                        <MedalIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    )}
                                    <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>
                                        {translate("navigation.challenges")}
                                    </div>
                                </Link>
                                <Link
                                    className={classNames(
                                        classes.dFlex,
                                        classes.flexCol,
                                        classes.alignItemsCenter,
                                        classes.justifyContentCenter,
                                        classes.mx4,
                                        classes.h100,
                                        classes.basicLink,
                                        location.pathname.includes("/eshop") && classes.navItemDesktopActive
                                    )}
                                    to={"/eshop"}>
                                    {location.pathname.includes("/eshop") && classes.navItemDesktopActive ? (
                                        <ShirtFilledIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    ) : (
                                        <ShirtIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                    )}
                                    <div className={classNames(classes.textMono, classes.textXs, dark ? classes.textWhite : classes.textSecondary)}>
                                        {translate("navigation.eshop")}
                                    </div>
                                </Link>
                                {/* ************ DROPDOWN MENU - NEMAZAT *********** */}
                                <div
                                    className={classNames(
                                        classes.dFlex,
                                        classes.flexCol,
                                        classes.alignItemsCenter,
                                        classes.justifyContentCenter,
                                        classes.mx4,
                                        classes.h100,
                                        classes.positionRelative,
                                        classes.cursorPointer,
                                        (location.pathname.includes("/profile") || location.pathname.includes("/profile-settings")) &&
                                            classes.navItemDesktopActive
                                    )}
                                    tabIndex="-1"
                                    onBlur={closeMenu}>
                                    <div className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter)} onClick={toggleMenu}>
                                        <MoreDotsIcon className={classNames(dark ? classes.textWhite : classes.textSecondary)} size={30} />
                                        <div
                                            className={classNames(
                                                classes.textMono,
                                                classes.textXs,
                                                dark ? classes.textWhite : classes.textSecondary
                                            )}>
                                            {menuOpen ? translate("less") : translate("more")}
                                        </div>
                                    </div>

                                    <Transition in={menuOpen} timeout={150} unmountOnExit={true}>
                                        {(state) => (
                                            <div className={classNames(classes.dropMenu)} style={{ ...transitionStyles[state] }}>
                                                <div className={classNames(classes.dFlex, classes.flexCol)}>
                                                    <Link
                                                        className={classNames(
                                                            classes.dFlex,
                                                            classes.alignItemsCenter,
                                                            classes.dropMenuItem,
                                                            classes.basicLink
                                                        )}
                                                        to={"/profile"}>
                                                        <UserIcon className={classNames(classes.textWhite, classes.mr2)} size={35} />
                                                        {translate("navigation.profile")}
                                                    </Link>
                                                    <Link
                                                        className={classNames(
                                                            classes.dFlex,
                                                            classes.alignItemsCenter,
                                                            classes.dropMenuItem,
                                                            classes.basicLink
                                                        )}
                                                        to={"/profile-settings"}>
                                                        <SettingsIcon className={classNames(classes.textWhite, classes.mr2)} size={35} />
                                                        {translate("navigation.settings")}
                                                    </Link>
                                                    <hr className={classes.dropMenuSeparator} />
                                                    <div
                                                        className={classNames(
                                                            classes.dFlex,
                                                            classes.alignItemsCenter,
                                                            classes.dropMenuItem,
                                                            classes.textOrange400
                                                        )}>
                                                        <LogoutIcon className={classNames(classes.textOrange400, classes.mr2)} size={35} />
                                                        {translate("navigation.signout")}
                                                    </div>
                                                </div>
                                            </div>
                                        )}
                                    </Transition>
                                </div>
                            </div>
                        )}

                        <div
                            className={classNames(classes.mlAuto, classes.mlLg0, classes.dFlex, classes.alignItemsCenter, classes.justifyContentEnd)}>
                            {user && (
                                <div
                                    className={classNames(
                                        classes.iconCounterHolder,
                                        classes.dFlex,
                                        classes.alignItemsCenter,
                                        classes.positionRelative,
                                        classes.mr2,
                                        classes.cursorPointer
                                    )}
                                    onClick={() => props.history.push("/notifications")}>
                                    <NotificationIcon
                                        className={classNames(dark ? classes.textWhite : classes.textSecondary, classes.dNone, classes.dLgBlock)}
                                        size={33}
                                    />
                                    <NotificationIcon className={classNames(classes.textSecondary, classes.dLgNone)} size={33} />
                                    {notificationItems > 0 && <div className={classNames(classes.iconCount)}>{notificationItems}</div>}
                                </div>
                            )}

                            <div
                                className={classNames(
                                    classes.iconCounterHolder,
                                    classes.dFlex,
                                    !user && classes.dLgNone,
                                    classes.alignItemsCenter,
                                    classes.positionRelative,
                                    classes.mr2,
                                    classes.cursorPointer
                                )}
                                onClick={() => props.history.push("/eshop/cart")}>
                                <BackpackIcon
                                    className={classNames(dark ? classes.textWhite : classes.textSecondary, classes.dNone, classes.dLgBlock)}
                                    size={33}
                                />
                                <BackpackIcon className={classNames(classes.textSecondary, classes.dLgNone)} size={33} />
                                {cartItems > 0 && <div className={classNames(classes.iconCount)}>{cartItems}</div>}
                            </div>

                            {isAdmin && (
                                <SelectField
                                    onChange={props.onChangeLocale}
                                    data={props.locales}
                                    value={props.locale}
                                    field=""
                                    textField="_id"
                                    required
                                />
                            )}

                            {!user && (
                                <div
                                    className={classNames(
                                        classes.dFlex,
                                        classes.dLgNone,
                                        classes.alignItemsCenter,
                                        classes.positionRelative,
                                        classes.cursorPointer
                                    )}
                                    onClick={() => props.history.push("/login")}>
                                    <ProfileIcon className={classNames(classes.textSecondary)} size={33} />
                                </div>
                            )}
                        </div>

                        {!user && (
                            <div className={classNames(classes.mlAuto, classes.dNone, classes.dLgFlex, classes.alignItemsCenter)}>
                                <Link
                                    to={"/landing"}
                                    className={classNames(
                                        classes.textMono,
                                        classes.textM,
                                        classes.mr7,
                                        classes.noUnderline,
                                        dark ? classes.textWhite : classes.textSecondary,
                                        location.pathname.includes("/landing") && classes.textPrimary
                                    )}>
                                    {translate("navigation.home")}
                                </Link>
                                <Link
                                    to={"/eshop"}
                                    className={classNames(
                                        classes.textMono,
                                        classes.textM,
                                        classes.mr7,
                                        classes.noUnderline,
                                        dark ? classes.textWhite : classes.textSecondary,
                                        location.pathname.includes("/eshop") && classes.textPrimary
                                    )}>
                                    {translate("navigation.eshop")}
                                </Link>
                                <Link
                                    to={"/login"}
                                    className={classNames(
                                        classes.textMono,
                                        classes.textM,
                                        classes.noUnderline,
                                        classes.dFlex,
                                        classes.alignItemsCenter,
                                        dark ? classes.textWhite : classes.textSecondary,
                                        location.pathname.includes("/login") && classes.textPrimary
                                    )}>
                                    {translate("navigation.signin")}
                                    <ProfileIcon className={classNames(classes.ml2)} size={25} />
                                </Link>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </>
    );
}

export default compose(withStyles(styles), withLocalization, withRouter)(Navbar);
