import React, { useState } from "react";
import { Transition } from "react-transition-group";
import { withStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router";
import { withLocalization } from "../../shared/localization";
import compose from "../../../compose";

import classNames from "classnames";
import getSharedStyles from "../../../style";

import HomeIcon from "../icons/Home";
import UserIcon from "../icons/User";
import MedalIcon from "../icons/Medal";
import MoreDotsIcon from "../icons/MoreDots";
import ShirtIcon from "../icons/TShirt";
import PlusIcon from "../icons/Plus";
import LogoutIcon from "../icons/Logout";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  bottomBar: {
    position: "fixed",
    height: "70px",
    width: "100%",
    bottom: "0",
    left: "0",
    background: theme.palette.secondary.main,
    zIndex: 800,
  },
  itemsHolder: {
    width: "calc((100% - 50px) / 2)",
    height: "100%",
    //background: 'red',
  },
  itemsHolderLeft: {
    paddingLeft: "0.75rem",
  },
  itemsHolderRight: {
    paddingRight: "0.75rem",
  },
  addPostHolder: {
    width: "50px",
  },
  addPostBtn: {
    width: "50px",
    height: "50px",
    background: theme.palette.primary.main,
    outline: "none!important",
    border: "0!important",
    color: "white",
    marginTop: "-2px",
    padding: "0",
  },
  "@media screen and (min-width: 350px)": {
    addPostBtn: {
      width: "66px",
      height: "66px",
      marginTop: "-4px",
    },
    addPostHolder: {
      width: "66px",
    },
    itemsHolder: {
      width: "calc((100% - 66px) / 2)",
    },
    itemsHolderLeft: {
      paddingLeft: "1.25rem",
    },
    itemsHolderRight: {
      paddingRight: "1.25rem",
    },
  },
  navItem: {
    width: "50%",
    textAlign: "center",
    paddingTop: "12px",
  },
  mobileMenu: {
    position: "absolute",
    zIndex: "1200",
    minWidth: "150px",
    background: theme.palette.secondary.main,
    bottom: "78px",
    right: "0px",
    transition: `opacity 150ms ease-in-out`,
    opacity: "0",
  },
  mobileMenuItem: {
    fontFamily: "'IBM Plex Mono', monospace",
    fontSize: "12px",
    color: "white",
    padding: "13px 15px",
  },
  mobileMenuSeparator: {
    width: "100%",
    border: "0",
    borderTop: "1px solid rgba(0,0,0,0.2)",
    margin: "0",
  },
});

function BottomNavigationBar(props) {
  const { classes, translate } = props;
  const [menuOpen, setMenuOpen] = useState(false);

  const goToLink = (name) => {
    props.history.push(name);
  };

  const closeMenu = () => {
    setMenuOpen(false);
  };

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  const transitionStyles = {
    entering: { opacity: 1 },
    entered: { opacity: 1 },
    exiting: { opacity: 0 },
    exited: { opacity: 0 },
  };

  return (
    <>
      <div className={classNames(classes.bottomBar, props.className)}>
        <div className={classNames(classes.dFlex, classes.h100)}>
          <div
            className={classNames(
              classes.itemsHolder,
              classes.itemsHolderLeft,
              classes.h100
            )}
          >
            <div
              className={classNames(
                classes.dFlex,
                classes.justifyContentCenter,
                classes.alignItemsCenter
              )}
            >
              <div
                className={classNames(
                  classes.dFlex,
                  classes.flexCol,
                  classes.navItem,
                  classes.alignItemsCenter
                )}
              >
                <HomeIcon className={classNames(classes.textWhite)} size={30} />
                <div
                  className={classNames(
                    classes.textMono,
                    classes.textWhite,
                    classes.textXs
                  )}
                >
                  {translate("navigation.home")}
                </div>
              </div>
              <div
                className={classNames(
                  classes.dFlex,
                  classes.flexCol,
                  classes.navItem,
                  classes.alignItemsCenter
                )}
              >
                <UserIcon className={classNames(classes.textWhite)} size={30} />
                <div
                  className={classNames(
                    classes.textMono,
                    classes.textWhite,
                    classes.textXs
                  )}
                >
                  {translate("navigation.profile")}
                </div>
              </div>
            </div>
          </div>
          <div
            className={classNames(classes.addPostHolder, classes.textCenter)}
          >
            <button className={classNames(classes.addPostBtn)}>
              <PlusIcon size={33} />
            </button>
          </div>
          <div
            className={classNames(
              classes.itemsHolder,
              classes.itemsHolderRight,
              classes.h100
            )}
          >
            <div
              className={classNames(
                classes.dFlex,
                classes.justifyContentCenter,
                classes.h100
              )}
            >
              <div
                className={classNames(
                  classes.dFlex,
                  classes.flexCol,
                  classes.navItem,
                  classes.alignItemsCenter
                )}
                onClick={() => goToLink("challenges")}
              >
                <MedalIcon
                  className={classNames(classes.textWhite)}
                  size={30}
                />
                <div
                  className={classNames(
                    classes.textMono,
                    classes.textWhite,
                    classes.textXs
                  )}
                >
                  {translate("navigation.challenges")}
                </div>
              </div>
              <div
                className={classNames(
                  classes.dFlex,
                  classes.flexCol,
                  classes.navItem,
                  classes.alignItemsCenter,
                  classes.positionRelative
                )}
                tabIndex="-1"
                onBlur={closeMenu}
              >
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.flexCol,
                    classes.alignItemsCenter
                  )}
                  onClick={toggleMenu}
                >
                  <MoreDotsIcon
                    className={classNames(classes.textWhite)}
                    size={30}
                  />
                  <div
                    className={classNames(
                      classes.textMono,
                      classes.textWhite,
                      classes.textXs
                    )}
                  >
                    {menuOpen ? translate("less") : translate("more")}
                  </div>
                </div>

                <Transition in={menuOpen} timeout={150} unmountOnExit={true}>
                  {(state) => (
                    <div
                      className={classNames(classes.mobileMenu)}
                      style={{ ...transitionStyles[state] }}
                    >
                      <div
                        className={classNames(classes.dFlex, classes.flexCol)}
                      >
                        <div
                          className={classNames(
                            classes.dFlex,
                            classes.alignItemsCenter,
                            classes.mobileMenuItem
                          )}
                        >
                          <ShirtIcon
                            className={classNames(
                              classes.textWhite,
                              classes.mr2
                            )}
                            size={35}
                          />
                          {translate("navigation.eshop")}
                        </div>
                        <hr className={classes.mobileMenuSeparator} />
                        <div
                          className={classNames(
                            classes.dFlex,
                            classes.alignItemsCenter,
                            classes.mobileMenuItem,
                            classes.textOrange400
                          )}
                        >
                          <LogoutIcon
                            className={classNames(
                              classes.textOrange400,
                              classes.mr2
                            )}
                            size={35}
                          />
                          {translate("navigation.signout")}
                        </div>
                      </div>
                    </div>
                  )}
                </Transition>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default compose(
  withStyles(styles),
  withRouter,
  withLocalization
)(BottomNavigationBar);
