import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";
import withStore from "../../consumers/withStore";
import withDoby from "../../consumers/withDoby";
import getSharedStyles from "../../style";
import compose from "../../compose";
import classNames from "classnames";
import Button from "./form/Button";
import moment from "moment";

import StopwatchIcon from "../dobyComponents/icons/Stopwatch";
import MountainIcon from "../dobyComponents/icons/Mountain";
// import StartIcon from '../dobyComponents/icons/Start';

const styles = (theme) => ({
  ...getSharedStyles(theme),

  card: {
    width: "100%",
    maxWidth: "400px",
    height: "500px",
    position: "relative",
    overflow: "hidden",
  },
  challengeImgHolder: {
    //height: '270px',
    position: "absolute",
    width: "100%",
    height: "100%",
    top: "0",
    left: "0",
    "&:after": {
      content: "''",
      display: "block",
      position: "absolute",
      bottom: "0",
      left: "0",
      width: "100%",
      height: "100%",
      background:
        "linear-gradient(180.41deg, rgba(24, 24, 24, 0.1) 22.25%, rgba(24,24,24, 0.5) 73.24%, rgba(24,24,24, 0.8) 84.72%, #191C1D 99.57%)",
    },
  },
  cardContent: {
    zIndex: 10,
    position: "relative",
  },
  challengeImg: {
    position: "center",
    left: "0",
    right: "0",
    width: "100%",
    height: "100%",
    objectFit: "cover",
  },

  logoHolder: {
    position: "absolute",
    top: "40px",
  },
  challengeLogo: {
    width: "150px",
  },

  difficultyDot: {
    width: "9px",
    height: "9px",
    borderRadius: "10px",
    marginRight: "5px",
    background: "white",
    opacity: "0.4",
  },
  dotFilled: {
    opacity: "1",
  },
  challengeBtn: {},
  challengeMap: {
    width: "90px",
  },
});

function ChallengeCard(props) {
  const [image, setImage] = useState(null);
  const [logo, setLogo] = useState(null);
  const [map, setMap] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      if (props.image) {
        const data = await props.doby.getFile(props.image);
        const reader = new FileReader();
        reader.onloadend = () => {
          setImage(reader.result);
        };
        reader.readAsDataURL(data);
      }

      if (props.logo) {
        const data = await props.doby.getFile(props.logo);
        const reader = new FileReader();
        reader.onloadend = () => {
          setLogo(reader.result);
        };
        reader.readAsDataURL(data);
      }

      if (props.map) {
        const data = await props.doby.getFile(props.map);
        const reader = new FileReader();
        reader.onloadend = () => {
          setMap(reader.result);
        };
        reader.readAsDataURL(data);
      }
    };

    fetchData();
  }, []);

  const { classes, difficulty, className, translate } = props;

  const summitsCount = props.summits && props.summits.length;
  const startDate = moment(props.start).format("DD.MM.YYYY");

  let months = moment(props.end).diff(moment(props.start), "months", true);
  months = Math.abs(months);
  months = Math.ceil(months);

  return (
    <div
      className={classNames(classes.card, className)}
      onClick={props.onClick && props.onClick}
    >
      <div className={classNames(classes.challengeImgHolder)}>
        <img src={image} className={classNames(classes.challengeImg)} alt="" />
      </div>
      <div
        className={classNames(
          classes.dFlex,
          classes.flexCol,
          classes.w100,
          classes.h100,
          classes.justifyContentBetween,
          classes.cardContent
        )}
      >
        <div
          className={classNames(
            classes.logoHolder,
            classes.w100,
            classes.textCenter
          )}
        >
          <img
            src={logo}
            className={classNames(classes.challengeLogo)}
            alt=""
          />
        </div>
        <div
          className={classNames(
            classes.p6,
            classes.dFlex,
            classes.flexCol,
            classes.h100,
            classes.justifyContentEnd
          )}
        >
          <div
            className={classNames(
              classes.mapHolder,
              classes.w100,
              classes.textCenter,
              classes.mb6
            )}
          >
            <img
              src={map}
              className={classNames(classes.challengeMap)}
              alt=""
            />
          </div>
          <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
            <MountainIcon size={25} className={classes.textWhite} />
            <div
              className={classNames(
                classes.textH4,
                classes.fontMedium,
                classes.textWhite,
                classes.textMono,
                classes.ml3
              )}
            >
              {summitsCount}{" "}
              {summitsCount === 1
                ? translate("challenge.summit")
                : summitsCount < 5
                ? translate("challenge.summits")
                : translate("challenge.summit_gen")}
            </div>
          </div>
          <div
            className={classNames(
              classes.dFlex,
              classes.alignItemsCenter,
              classes.mt2
            )}
          >
            <StopwatchIcon size={25} className={classes.textWhite} />
            <div
              className={classNames(
                classes.textH4,
                classes.fontMedium,
                classes.textWhite,
                classes.textMono,
                classes.ml3
              )}
            >
              {months}{" "}
              {months === 1
                ? translate("challenge.month")
                : months < 5
                ? translate("challenge.months")
                : translate("challenge.month_gen")}
            </div>
          </div>
          {/* <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt1)}>
                        <StartIcon size={25}/>
                        <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                            Start {startDate}
                        </div>
                    </div> */}
          <div
            className={classNames(
              classes.dFlex,
              classes.w100,
              classes.justifyContentBetween,
              classes.mt7
            )}
          >
            <div className={classNames(classes.dFlex, classes.flexCol)}>
              <div className={classNames(classes.textEl250, classes.textS)}>
                {translate("challenge.difficulty")}
              </div>
              <div
                className={classNames(
                  classes.dFlex,
                  classes.alignItemsCenter,
                  classes.mt2
                )}
              >
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 1 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 2 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 3 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 4 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 5 && classes.dotFilled
                  )}
                ></div>
              </div>
            </div>
            <div className={classNames(classes.dFlex, classes.flexCol)}>
              <div className={classNames(classes.textEl250, classes.textS)}>
                {translate("challenge.start")}
              </div>
              <div
                className={classNames(
                  classes.textM,
                  classes.textWhite,
                  classes.fontSemibold,
                  classes.mt1
                )}
              >
                {startDate}
              </div>
            </div>
            {/* <img src={'./img/map-high-tatras.svg'} className={classNames(classes.challengeMap)}/> */}
          </div>
        </div>
        {props.actionBtn !== false && (
          <Button
            className={classNames(classes.challengeBtn)}
            fullwidth
            arrow
            onClick={props.onClick}
          >
            {translate("challenge.more")}
          </Button>
        )}

        {/* <div className={classNames(classes.cardContent, classes.dFlex, classes.flexCol, classes.w100, classes.p5)}>
                    <div className={classNames(classes.w100, classes.dFlex, classes.alignItemsCenter, classes.justifyContentBetween)}>
                        <div className={classNames(classes.dFlex, classes.flexCol)}>
                            <div className={classNames(classes.dFlex, classes.flexCol)}>
                                <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mb1)}>
                                    <div className={classNames(classes.difficultyDot, difficulty >= 1 && classes.dotFilled)}></div>
                                    <div className={classNames(classes.difficultyDot, difficulty >= 2 && classes.dotFilled)}></div>
                                    <div className={classNames(classes.difficultyDot, difficulty >= 3 && classes.dotFilled)}></div>
                                    <div className={classNames(classes.difficultyDot, difficulty >= 4 && classes.dotFilled)}></div>
                                    <div className={classNames(classes.difficultyDot, difficulty >= 5 && classes.dotFilled)}></div>
                                </div>
                                <div className={classNames(classes.textSecondaryLight, classes.textMono, classes.textM, classes.fontMedium)}>
                                    Obtiaznost
                                </div>
                            </div>
                            <div className={classNames(classes.dFlex, classes.flexCol, classes.mt4)}>
                                <div className={classNames(classes.textWhite, classes.fontSemibold, classes.textM)}>
                                    {startDate}
                                </div>
                                <div className={classNames(classes.textSecondaryLight, classes.textMono, classes.textM, classes.fontMedium)}>
                                    Start
                                </div>
                            </div>
                        </div>
                        <div className={classNames(classes.challengeLogoHolder)}>
                            <img src={'./img/newLogo.png'} className={classNames(classes.challengeLogo)}/>
                        </div>
                    </div>
                    <div className={classNames(classes.textSecondaryLight, classes.textM, classes.mt3)}>
                        {props.description}
                    </div>
                    <Button className={classNames(classes.mt3)} fullwidth>
                        Zobrazit vyzvu
                    </Button>
                </div> */}
      </div>
    </div>
  );
}

ChallengeCard.propTypes = {
  className: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string,
  start: PropTypes.string,
  difficulty: PropTypes.number,
};

export default compose(
  withStyles(styles),
  withLocalization,
  withStore,
  withDoby
)(ChallengeCard);
