import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    medalCard: {
        width: '120px'
    },
    medalLogoHolder: {
        height: '55px'
    },
    medalLogo: {
        maxWidth: '74px',        
        height: 'auto',
    },
    medalHolder: {
        width: '120px',
        height: '155px',
        position: 'relative',
        marginTop: '5px',
        '&:after': {
            content: "''",
            display: 'block',
            position: 'absolute',
            width: '100%',
            height: '120px',
            background: '#F2F2F2',
            top: '8px',
            left: '0',
        }
        // height: '120px',
        // width: '120px',
        // background: '#F2F2F2',
        // position: 'relative'
    },
    medalImg:{ 
        position: 'absolute',
        width: '100px',
        height: 'auto',
        top: '0',
        left: '0',
        right: '0',
        margin: '0 auto',
        zIndex: '5'
    }
});

function UserMedal(props) {
    const { classes } = props;

    return (
        <>
            <div className={classNames(classes.medalCard, classes.dFlex, classes.flexCol, classes.alignItemsCenter)}>
                <div className={classNames(classes.medalLogoHolder)}>
                    <img alt="challenge-medal-logo" src={'/img/challenge-logo.png'} className={classNames(classes.medalLogo)} />
                </div>
                <div className={classNames(classes.medalHolder)}>
                    <img alt="challenge-medal" src={'/img/new-medal.png'} className={classNames(classes.medalImg)} />
                </div>

            </div>           
        </>
    );
}

export default compose(withStyles(styles), withLocalization)(UserMedal);
