import React from "react";
import { withStyles } from "@material-ui/core/styles";

import compose from "../../../compose";
import { withLocalization } from "../../shared/localization";
import { withRouter } from "react-router";

import classNames from "classnames";
import withStore from "../../../consumers/withStore";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import withMail from "../../../consumers/withMail";
import getSharedStyles from "../../../style";
import Button from "../../dobyComponents/form/Button";
import CircleLoader from "../../dobyComponents/CircleLoader";
import config from "../../../config";
import moment from "moment";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  emptyCartImg: {
    width: "60%",
  },
});

class VerificationPayment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      succeeded: false,
    };
  }

  componentDidMount() { 
    this.fetchData();
  }

  fetchData = async () => {
    const { doby } = this.props;

    const query = new URLSearchParams(this.props.location.search);
    if (query) {
      const id = query.get("id");
      const success = query.get("success");

      if (success) {
        this.setState({ succeeded: success && success === "true" });
        this.sendMail(
          doby.checkout.orderData.items.find((i) => i.challengeRef !== null)
        );
        doby.cartRemoveProducts();
        return;
      }

      if (id) {
        const result = await doby.verifyOrder(id);
        if (result) {
          this.setState({ succeeded: true });
          this.sendMail(result.items.find((i) => i.challengeRef !== null));
          doby.cartRemoveProducts();
        }
      }
    }
  };

  sendMail = async (challenge) => {
    const { translate, user, mail, doby } = this.props;

    let res = null;
    if (challenge) res = await doby.getChallenge(challenge.challengeRef.id);
    if (res) {
      const { _id, summits, end, start, difficulty, name } = res;

      const dates = this.handleDates(start, end);
      const subject = translate("email_challenge_bought_subject");

      const data = {
        subject: subject.replace("{0}", name),
        from: config.mail.address,
        fromName: config.mail.name,
        recipientRef: {
          id: user.user._id,
        },
        ru: "/challenge-detail/" + _id,
        templateId: config.mail.challengeBoughtTemplate,
        params: [
          `${dates.months}${" "}
            ${
              dates.months === 1
                ? translate("challenge.month")
                : dates.months < 5
                ? translate("challenge.months")
                : translate("challenge.month_gen")
            }`,
          summits.length,
          dates.start,
          dates.end,
          difficulty,
          summits.map((s) => s.name).join(","),
        ],
      };

      await mail.sendMail(data);
    }
  };

  handleDates = (sDateFrom, sDateTo) => {
    let months = moment(sDateFrom).diff(moment(sDateTo), "months", true);
    months = Math.abs(months);
    months = Math.ceil(months);

    return {
      start: moment(sDateFrom).format("DD.MM.YYYY"),
      end: moment(sDateTo).format("DD.MM.YYYY"),
      months,
    };
  };

  goToEshop = () => this.props.history.push(`/eshop`);

  render = () => {
    const { classes } = this.props;
    return (
      <>
        <div className={classNames(classes.flexCol, classes.container)}>
          {this.props.doby.checkout.isLoading ? (
            <CircleLoader center className={classNames(classes.py8)} />
          ) : (
            <>
              <div className={classNames(classes.mt8, classes.textCenter)}>
                <img
                  className={classes.emptyCartImg}
                  alt=""
                  src={
                    this.state.succeeded
                      ? "https://cdn3.iconfinder.com/data/icons/flat-actions-icons-9/792/Tick_Mark_Dark-512.png"
                      : "https://nika.shop/wp-content/uploads/2020/01/fail-png-7.png"
                  }
                />
              </div>
              <div className={classNames(classes.textCenter, classes.mt4)}>
                {this.props.translate(
                  this.state.succeeded
                    ? "payment_successfull"
                    : "payment_failed"
                )}
              </div>
            </>
          )}

          <Button
            className={classNames(classes.mt4)}
            onClick={this.goToEshop}
            fullwidth
          >
            {this.props.translate("go_to_eshop")}
          </Button>
        </div>
      </>
    );
  };
}

export default compose(
  withStyles(styles),
  withLocalization,
  withStore,
  withDoby,
  withUser,
  withMail,
  withRouter
)(VerificationPayment);
