import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import Modal from "@material-ui/core/Modal";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  modalWrapper: {
    background: "white",
    padding: "1rem",
    width: "700px",
    maxWidth: "100%",
    maxHeight: "100vh",
    margin: "0 auto",
    outline: "none!important",
    position: "relative",
    [theme.breakpoints.up("lg")]: {
      marginTop: "30px",
    },
    [theme.breakpoints.up("xl")]: {
      marginTop: "60px",
    },
  },
  modalOverlay: {
    background: "rgba(0,38,62,0.75)",
  },
});

function DobyModal(props) {
  const { classes } = props;
  return (
    <Modal
      open={props.isOpen}
      onClose={props.onClose}
      onBackdropClick={props.onClose}
      BackdropProps={{
        classes: {
          root: classes.modalOverlay,
        },
      }}
    >
      <div className={classNames(classes.modalWrapper, props.wrapperClassName)}>
        {props.children}
      </div>
    </Modal>
  );
}

export default compose(withStyles(styles), withLocalization)(DobyModal);
