import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";
import Grid from "@material-ui/core/Grid";
import ChevronLeft from "../dobyComponents/icons/ChevronLeft";
import {
   useHistory
} from "react-router-dom";

const styles = (theme) => ({
    ...getSharedStyles(theme),
});

function PageHeader(props) {
    const { classes } = props;
    const history = useHistory();

    return (
        <>
            <div className={classNames(classes.bgLight, classes.py5)}>
                <div className={classNames(classes.container)}>
                    <Grid container>
                        <Grid item xs={12} className={classNames(classes.mb3)}>
                            <div className={classNames(classes.backBtn)} onClick={() => history.goBack()}>
                                <ChevronLeft size={27} />
                                {props.translate("back")}
                            </div>
                        </Grid>

                        <Grid item xs={12}>
                            <h3 className={classNames(classes.uppercase)}>{props.title}</h3>
                        </Grid>
                    </Grid>
                </div>
            </div>
        </>
    );
}

export default compose(withStyles(styles), withLocalization)(PageHeader);
