import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../../style";
import classNames from "classnames";
import { withLocalization } from "../../shared/localization";
import compose from "../../../compose";
import { Grid } from "@material-ui/core";
import { Link } from "react-router-dom";

import InstagramIcon from "../../dobyComponents/icons/Instagram";
import FacebookIcon from "../../dobyComponents/icons/Facebook";
import EnvelopeIcon from "../../dobyComponents/icons/Envelope";
import TextField from "../../dobyComponents/form/TextField";
import CheckboxConditions from "../../dobyComponents/form/CheckboxConditions";
import Button from "../../dobyComponents/form/Button";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    copyrightBar: {
        background: "rgba(0, 0, 0, 0.1)",
        padding: "1rem 0",
    },
    copyrightText: {
        color: "#075985",
    },
    subNotify: {
        display: "inline-block",
        padding: "10px 15px",
    },
    notifySuccess: {
        background: "#edffe1",
        color: "#477942",
        border: "2px solid #cee8cc",
    },
    notifyError: {
        background: "#ffe0e0",
        color: "#cc6767",
        border: "2px solid #ffb8b8",
    },
});

class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            checkConditions: {
                value: false,
                errorText: "",
            },
            email: {
                value: "",
                errorText: "",
            },
            subscribeShowSuccess: false,
            subscribeShowError: false,
        };
    }
    componentDidMount() {}

    handleCheckChange = (name, value) => {
        this.setState({
            checkConditions: {
                value: value,
                errorText: "",
            },
        });
    };

    handleEmailChange = (name, value) => {
        this.setState({
            email: {
                value: value,
                errorText: "",
            },
        });
    };

    validateEmail = (email) => {
        const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return email !== "" && regex.test(email);
    };

    handleSubscribe = async () => {
        const { translate } = this.props;
        const { email, checkConditions } = this.state;
        const emailValid = this.validateEmail(email.value);

        if (emailValid) {
            if (checkConditions.value) {
                try {
                    /*const result = */ await this.props.doby.subscribe({
                        email: email.value,
                    });
                    this.setState({
                        email: { value: "", errorText: "" },
                        checkConditions: { value: false, errorText: "" },
                        subscribeShowSuccess: true,
                    });
                    setTimeout(() => {
                        this.setState({
                            subscribeShowSuccess: false,
                        });
                    }, 4000);
                } catch (error) {
                    this.setState({
                        subscribeShowError: true,
                    });
                    setTimeout(() => {
                        this.setState({
                            subscribeShowError: false,
                        });
                    }, 4000);
                }
            } else {
                this.setState((state) => ({
                    checkConditions: {
                        value: state.checkConditions.value,
                        errorText: translate("validation.checkboxNotChecked"),
                    },
                }));
            }
        } else {
            this.setState((state) => ({
                email: {
                    value: state.email.value,
                    errorText: translate("validation.emailNotValid"),
                },
            }));
        }
    };

    render() {
        const { classes, translate } = this.props;

        return (
            <>
                <div className={classNames(classes.w100, classes.dFlex, classes.flexCol, classes.bgSecondary)}>
                    <div className={classNames(classes.w100, classes.py10)}>
                        <div className={classNames(classes.container)}>
                            <Grid container className={classNames(classes.justifyContentCenter, classes.justifyContentSmBetween)} spacing={16}>
                                <Grid item xs={12} sm={5} md={3} lg={3} className={classNames()}>
                                    <div className={classNames(classes.dFlex, classes.flexCol)}>
                                        <Link
                                            className={classNames(classes.textMono, classes.textWhite, classes.fontMedium, classes.noUnderline)}
                                            to={"/challenges"}>
                                            {translate("navigation.challenges")}
                                        </Link>
                                        <Link
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.mt4
                                            )}
                                            to={"/eshop"}>
                                            {translate("navigation.eshop")}
                                        </Link>
                                        <Link
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.mt4
                                            )}
                                            to={"/login"}>
                                            {translate("navigation.signin")}
                                        </Link>
                                        <Link
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.mt7
                                            )}
                                            to={""}>
                                            {translate("footer.businessConditions")}
                                        </Link>
                                        <Link
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.mt4
                                            )}
                                            to={""}>
                                            {translate("footer.forDownload")}
                                        </Link>
                                    </div>
                                </Grid>
                                <Grid item lg={1} className={classNames(classes.dNone, classes.dLgBlock)}></Grid>
                                <Grid item xs={12} sm={7} md={4} lg={3} className={classNames(classes.mt9, classes.mtSm0)}>
                                    <h5 className={classNames(classes.textWhite, classes.uppercase)}>{translate("footer.joinSubs")}</h5>
                                    <div className={classNames(classes.textWhite, classes.mt3, classes.textM)}>{translate("footer.stayTuned")}</div>

                                    <div className={classNames(classes.mt4)}>
                                        <TextField
                                            onChange={this.handleEmailChange}
                                            fullWidth
                                            required
                                            value={this.state.email.value}
                                            type="email"
                                            field="email"
                                            errorText={this.state.email.errorText}
                                        />

                                        <CheckboxConditions
                                            onChange={this.handleCheckChange}
                                            fullWidth
                                            required
                                            value={this.state.checkConditions.value}
                                            field="checkConditions"
                                            className={classNames(classes.mt2)}
                                            errorText={this.state.checkConditions.errorText}
                                            light
                                        />

                                        <Button className={classNames(classes.mt3)} onClick={this.handleSubscribe} arrow>
                                            {translate("footer.subscribe")}
                                        </Button>

                                        {(this.state.subscribeShowSuccess || this.state.subscribeShowError) && (
                                            <div
                                                className={classNames(
                                                    classes.subNotify,
                                                    classes.mt3,
                                                    this.state.subscribeShowSuccess && classes.notifySuccess,
                                                    this.state.subscribeShowError && classes.notifyError
                                                )}>
                                                {this.state.subscribeShowSuccess
                                                    ? translate("subscribe.success")
                                                    : this.state.subscribeShowError
                                                    ? translate("subscribe.error")
                                                    : ""}
                                            </div>
                                        )}
                                    </div>
                                </Grid>
                                <Grid item lg={1} className={classNames(classes.dNone, classes.dLgBlock)}></Grid>
                                <Grid item xs={12} sm={6} md={4} lg={3}className={classNames(classes.mt9, classes.mtMd0)}>
                                    <h5 className={classNames(classes.textWhite, classes.uppercase)}>{translate("footer.contact")}</h5>
                                    <div className={classNames(classes.textWhite, classes.mt3, classes.dFlex, classes.flexCol)}>
                                        <a
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.dFlex,
                                                classes.alignItemsCenter
                                            )}
                                            href={`mailto:${translate("contact.email")}`}>
                                            <EnvelopeIcon size={25} className={classes.mr2} />
                                            {translate("contact.email")}
                                        </a>
                                        <a
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.dFlex,
                                                classes.alignItemsCenter,
                                                classes.mt3
                                            )}
                                            href={translate("contact.instagram_link")}
                                            target="_blank">
                                            <InstagramIcon size={25} className={classes.mr2} />
                                            Instagram
                                        </a>
                                        <a
                                            className={classNames(
                                                classes.textMono,
                                                classes.textWhite,
                                                classes.fontMedium,
                                                classes.noUnderline,
                                                classes.dFlex,
                                                classes.alignItemsCenter,
                                                classes.mt3
                                            )}
                                            href={translate("contact.fb_linkk")}
                                            target="_blank">
                                            <FacebookIcon size={25} className={classes.mr2} />
                                            Facebook
                                        </a>
                                    </div>
                                </Grid>
                            </Grid>
                        </div>
                    </div>
                    <div className={classNames(classes.copyrightBar, classes.w100)}>
                        <div className={classNames(classes.container)}>
                            <Grid container className={classNames(classes.justifyContentCenter, classes.justifyContentSmBetween)}>
                                <Grid item xs={12} sm={3} className={classNames()}></Grid>
                                <Grid item xs={12} sm={9} className={classNames(classes.textCenter, classes.textSmRight)}>
                                    <div className={classNames(classes.textMono, classes.textM, classes.fontSemibold, classes.copyrightText)}>
                                        Vytvoren s lskou k prrode. #uprachory Copyright  Doby Hory 2021. Vetky prva vyhraden
                                    </div>
                                </Grid>
                            </Grid>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default compose(withStyles(styles), withLocalization)(Footer);
