import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../../compose";

import classNames from "classnames";
import getSharedStyles from "../../../../style";

import FbIcon from "../../icons/Facebook";
import { withLocalization } from "../../../shared/localization";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  socialConnectCard: {
    background: "#1778F2",
  },
});

function FacebookConnection(props) {
  const { classes, onlyIcon } = props;

  const handleClick = () => {
    const redirect = process.env.REACT_APP_HOMEPAGE;
    const url = `https://www.facebook.com/v9.0/dialog/oauth?scope=email&client_id=${props.config.id}&redirect_uri=${redirect}/?provider=5&state=1`;
    window.location.replace(url);
  };

  if (!props.config) {
    return null;
  }

  return (
    <>
      <div
        className={classNames(
          classes.p3,
          classes.socialConnectCard,
          classes.dBlock,
          classes.cursorPointer,
          props.className
        )}
        onClick={handleClick}
      >
        <div
          className={classNames(
            classes.dFlex,
            classes.w100,
            classes.alignItemsCenter,
            onlyIcon && classes.justifyContentCenter
          )}
        >
          <FbIcon size={30} className={classNames(classes.textWhite)} />
          {!onlyIcon && (
            <div className={classNames(classes.flexCol, classes.ml4)}>
              <div
                className={classNames(classes.fontMedium, classes.textWhite)}
              >
                {props.translate("fbRegister")}
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

export default compose(
  withStyles(styles),
  withLocalization
)(FacebookConnection);
