import React from "react";
//import PropTypes from 'prop-types';
import { withStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router";
import getSharedStyles from "../../style";
import Grid from "@material-ui/core/Grid";
import classNames from "classnames";

import compose from "../../compose";
import withUser from "../../consumers/withUser";
import { withLocalization } from "../shared/localization";

import ChallengeCardMyStarted from "../dobyComponents/ChallengeCardMyStarted";
import ChallengeCardV2 from "../dobyComponents/ChallengeCardV2";
import withDoby from "../../consumers/withDoby";
import FlickitySlider from "../dobyComponents/FlickitySlider";
import PageHeader from '../dobyComponents/PageHeader'
import CircleLoader from "../dobyComponents/CircleLoader";
import FooterBig from "../dobyComponents/footer/FooterBig";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    challengeCard: {
        width: "300px",
    },
});

class Challenges extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myStartedChallenges: [],
            notParticipateChallenges: [],

            myChallengesLoading: true,
            challengesLoading: true,

        };
    }

    async componentDidMount() {
        const { doby, user } = this.props;
        await doby.getUser(user.user._id);
        const dobyUser = this.props.doby.user;
        this.setMyStartedChallenges(dobyUser);
        this.setNotParticipateChallenges(dobyUser);
    }

    setMyStartedChallenges = (dobyUser) => {
        if(dobyUser) {
            const { challenges, challengesRef } = dobyUser;

            let unfinishedChallenges = [];
            let unfinishedChallengesRef = [];
            if (challengesRef) {
                unfinishedChallengesRef = challengesRef.filter((c) => c.finished === null).map((c) => ({ id: c.id, count: c.count }));
            }
    
            unfinishedChallenges = unfinishedChallengesRef.map((activeC) => {
                const found = challenges.find((c) => c._id === activeC.id);
                return {
                    ...found,
                    countFinishedSummits: activeC.count,
                };
            }); 
            
            this.setState({ myStartedChallenges: unfinishedChallenges});
        }

        this.setState({myChallengesLoading: false });
    };

    setNotParticipateChallenges = async (dobyUser) => {
        if(dobyUser) {
            let notParticipateChallenges;
            const filter = [{ field: "end", value: new Date().toISOString(), op: "gt" }];
            const filteredChals = await this.props.doby.getChallenges(filter, { field: "name", dir: "asc" }, { limit: 1000, skip: 0 }, false);

            if (dobyUser) {
                const userChals = dobyUser.challenges.map((c) => c._id);
                notParticipateChallenges = filteredChals
                    ? filteredChals.data.filter((filteredChal) => userChals.find((userChal) => userChal._id === filteredChal._id) === undefined)
                    : [];
            }
            else {
                notParticipateChallenges = filteredChals.data;
            }
            this.setState({ notParticipateChallenges });
        }

        this.setState({ challengesLoading: false });
    };

    renderMyStartedChallenges = () => {
        const { classes } = this.props;
        return this.state.myStartedChallenges.map((challenge) => {
            return (
                <Grid item xs={12} sm={6} lg={4}>
                    <ChallengeCardMyStarted
                        title={challenge.name}
                        start={challenge.start}
                        end={challenge.end}
                        summits={challenge.summitsRef}
                        image={challenge.imageId}
                        logo={challenge.logoId}
                        finishedCount={challenge.countFinishedSummits}
                        className={classNames()}
                        onClick={() => challenge._id && this.props.history.push(`/challenge-detail/${challenge._id}`)}
                    />
                </Grid>
            );
        });
    };

    renderNotParticipateChallenges = () => {
        const { classes } = this.props;
        return this.state.notParticipateChallenges.map((challenge) => {
            return (
                <div className={"carousel-cell"} key={challenge._id}>
                    <ChallengeCardV2
                        title={challenge.name}
                        description={challenge.description}
                        start={challenge.start}
                        end={challenge.end}
                        difficulty={challenge.difficulty}
                        summits={challenge.summitsRef}
                        image={challenge.imageId}
                        logo={challenge.logoId}
                        map={challenge.mapId}
                        className={classNames(classes.challengeCard, classes.mt2, classes.mb2)}
                        onClick={() => challenge._id && this.props.history.push(`/challenge-detail/${challenge._id}`)}
                    />
                </div>
            );
        });
    };

    render() {
        const { classes } = this.props;
        return (
            <>               
                <PageHeader title={this.props.translate('navigation.challenges')}/>

                <div className={classNames(classes.container, classes.py8, classes.pyLg10)}>
                    <Grid container>
                        <Grid item xs={12} className={classNames(classes.mb3, classes.mbMd5)}>
                            <h4 className={classNames(classes.uppercase)}>Moje vyzvy</h4>
                        </Grid>
                    </Grid>
                    {this.state.myChallengesLoading ? 
                        (<CircleLoader center className={classNames(classes.py8)}/>)
                    : (
                        <Grid container spacing={24}>
                            {this.renderMyStartedChallenges()}
                        </Grid>
                    )}
                </div>

                <div className={classNames(classes.bgSecondary, classes.mb10, classes.mt5)}>
                    <div className={classNames(classes.container, classes.py9, classes.pyMd10)}>
                        <Grid container direction="row" justify="center">
                            <Grid item xs={12} className={classNames()}>
                                <h4 className={classNames(classes.textWhite, classes.uppercase)}>{this.props.translate("upcoming_challenges")}</h4>
                            </Grid>
                            <Grid item xs={12} className={classNames()}>
                                {this.state.challengesLoading ? (
                                    <CircleLoader center className={classNames(classes.py8)}/>
                                ) : (
                                    <div className={classNames(classes.carouselFullwidth)}>
                                        <FlickitySlider className={classNames(classes.carouselSpacing, classes.mt4)}>
                                            {this.renderNotParticipateChallenges()}
                                        </FlickitySlider>
                                    </div>
                                )}
                            </Grid>
                        </Grid>
                    </div>
                </div>

                <FooterBig />
            </>
        );
    }
}

export default compose(withStyles(styles), withUser, withRouter, withLocalization, withDoby)(Challenges);
