import React from "react";
import { withRouter } from "react-router";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
//import withDoby from '../../consumers/withDoby';
import withUser from "../../consumers/withUser";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";
import Button from "../dobyComponents/form/Button";
import Grid from "@material-ui/core/Grid";

import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";

import ActivityPost from "../dobyComponents/ActivityPost";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  filterIconHolder: {
    width: "60px",
  },
  tabsRootClasses: {
    background: "#F1F1F1",
    border: "3px solid #F1F1F1",
  },
  tabsInicator: {
    height: "100%",
    background: "#333442",
  },
  tabSelected: {
    zIndex: 3,
    color: "white",
  },
  filterOverlay: {
    position: "fixed",
    height: "100%",
    width: "100%",
    background: "#F7F7F7",
    top: 0,
    left: 0,
    zIndex: 15,
    paddingTop: "60px",
  },
});

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTab: 0,
      filterOpen: false,
    };
  }

  componentDidMount() {}

  handleTabChange = (event, tab) => {
    this.setState({ currentTab: tab });
  };

  renderActivityPosts = () => {
    const { classes } = this.props;
    let activityPosts = [0, 1, 2, 3];

    return activityPosts.map((post) => {
      return <ActivityPost className={classNames(classes.mb3)} />;
    });
  };

  openFilter = () => {
    console.log("aaa");
    this.setState({ filterOpen: true });
  };

  applyFilters = () => {
    this.setState({ filterOpen: false });
  };

  render() {
    const { classes, translate } = this.props;

    return (
      <>
        <Grid container direction="row" justify="center" alignItems="center">
          <Grid
            item
            xs={12}
            className={classNames(classes.pt5, classes.pb2, classes.bgWhite)}
          >
            <div className={classNames(classes.container)}>
              <div
                className={classNames(
                  classes.dFlex,
                  classes.w100,
                  classes.alignItemsCenter
                )}
              >
                <div className={classNames(classes.flexGrow)}>
                  <Tabs
                    value={this.state.currentTab}
                    onChange={this.handleTabChange}
                    aria-label="simple tabs example"
                    variant="fullWidth"
                    classes={{
                      root: classes.tabsRootClasses,
                      indicator: classNames(
                        classes.tabsInicator,
                        classes.roundedMd
                      ),
                    }}
                    className={classNames(classes.roundedMd)}
                  >
                    <Tab
                      label={translate("following")}
                      classes={{
                        selected: classes.tabSelected,
                        root: classNames(
                          classes.textXs2,
                          classes.fontMedium,
                          classes.capitalize
                        ),
                      }}
                    />
                    <Tab
                      label={translate("all")}
                      classes={{
                        selected: classes.tabSelected,
                        root: classNames(
                          classes.textXs2,
                          classes.fontMedium,
                          classes.capitalize
                        ),
                      }}
                    />
                    <Tab
                      label={translate("my")}
                      classes={{
                        selected: classes.tabSelected,
                        root: classNames(
                          classes.textXs2,
                          classes.fontMedium,
                          classes.capitalize
                        ),
                      }}
                    />
                  </Tabs>
                </div>
                <div
                  className={classNames(
                    classes.filterIconHolder,
                    classes.textRight
                  )}
                  onClick={this.openFilter}
                >
                  icon
                </div>
              </div>
            </div>
          </Grid>

          <Grid item xs={12} className={classNames()}>
            {this.renderActivityPosts()}
          </Grid>
        </Grid>

        {this.state.filterOpen && (
          <div className={classNames(classes.filterOverlay)}>
            <div className={classNames(classes.container)}>
              <div
                className={classNames(
                  classes.fontBold,
                  classes.textMd,
                  classes.mt5
                )}
              >
                {translate("filter")}
              </div>
              <Button fullwidth onClick={this.applyFilters}>
                {translate("useFilters")}
              </Button>
            </div>
          </div>
        )}
      </>
    );
  }
}

export default compose(
  withStyles(styles),
  withUser,
  withLocalization,
  withRouter
)(Home);
