import React from "react";
import PropTypes from "prop-types";

//import Avatar from "@material-ui/core/Avatar";
//import Divider from "@material-ui/core/Divider";
import CssBaseline from "@material-ui/core/CssBaseline";
import FormControl from "@material-ui/core/FormControl";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import Typography from "@material-ui/core/Typography";
import classNames from "classnames";
import withStyles from "@material-ui/core/styles/withStyles";
import { withRouter } from "react-router";
import { withLocalization } from "../shared/localization";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import getSharedStyles from "../../style";

import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import config from "../../config";

import TextField from "../dobyComponents/form/TextField";
import Button from "../dobyComponents/form/Button";
import FbConnect from "../dobyComponents/socialConnections/login/FacebookConnection";
import GoogleConnect from "../dobyComponents/socialConnections/login/GoogleConnection";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  mobileLoginImage: {
    paddingTop: "130px",
    position: "relative",
    "&:after": {
      [theme.breakpoints.down("xs")]: {
        content: "''",
        display: "block",
        position: "absolute",
        width: "100%",
        height: "250px",
        top: "0",
        left: "0",
        backgroundImage: `url(/img/login-bg-mobile.jpg)`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundPosition: "bottom center",
        zIndex: "1",
      },
    },
  },
  loginContent: {
    position: "relative",
    zIndex: "2",
  },
  socialLoginItem: {
    width: "47%",
  },
  loginSeparator: {
    flexGrow: "1",
    position: "relative",
    "&:after": {
      content: "''",
      position: "absolute",
      display: "block",
      width: "100%",
      height: "1px",
      background: "rgba(0, 0, 0, 0.08)",
      top: "50%",
    },
  },
  loginCol: {
    maxWidth: "450px",
  },
  loginBg: {
    backgroundImage: `url(/img/${config.locale}/intro-bg-desktop.jpg)`,
    backgroundRepeat: "no-repeat",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
});

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href={process.env.REACT_APP_HOMEPAGE}>
        {process.env.REACT_APP_NAME + " "}
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      token: "",
      disabled: false,
    };
  }

  handleSubmit = async (e) => {
    e.preventDefault();

    this.setState({ disabled: true });

    const { username, password, token } = this.state;
    try {
      const user = await this.props.user.auth(
        username.trim().toLowerCase(),
        password,
        token
      );
      if (user === null) {
        this.props.user.showMsg("Unauthorized");
      }
    } catch (error) {
      this.props.user.showError(error);
    }

    this.setState({ disabled: false });
  };

  handleChange = (name, value) => this.setState({ [name]: value });

  handleClickReset = async () => {
    const { username } = this.state;

    if (username) {
      try {
        const resetObject = { 
          username: username.trim().toLowerCase(),
          redirectUri: process.env.REACT_APP_HOMEPAGE,
          from: config.mail.address,
          fromName: config.mail.name,
          subject: this.props.translate('email_reset_password'),
          templateId: config.mail.resetPasswordTemplate
        };
        
        await this.props.user.reset(resetObject);
        this.props.user.showMsg("Mail sended.");
      } catch (error) {
        this.props.user.showError(error);
      }
    }
  };

  goToRegister = () => {
    this.props.history.push(`/register`);
  };

  render() {
    const { classes, user, translate } = this.props;
    const { username, password, disabled, oAuth, token } = this.state;

    const { from } = this.props.location.state || {
      from: { pathname: "/landing" },
    };

    if (oAuth) return null;
    if (user.user) return <Redirect to={from} />;

    return (
      <>
        <Grid container className={classNames(classes.h100)}>
          <Grid item xs={12} sm={7} md={6}>
            <div
              className={classNames(
                classes.bgLight,
                classes.mobileLoginImage,
                classes.h100
              )}
            >
              <Grid
                container
                className={classNames(classes.h100)}
                justify="center"
              >
                <Grid
                  item
                  xs={12}
                  sm={10}
                  md={8}
                  lg={7}
                  xl={5}
                  className={classNames(classes.loginCol)}
                >
                  <div
                    className={classNames(
                      classes.container,
                      classes.h100,
                      classes.loginContent
                    )}
                  >
                    <h4 className={classNames(classes.uppercase)}>
                      {translate("signIn")}
                    </h4>

                    <div
                      className={classNames(
                        classes.dFlex,
                        classes.w100,
                        classes.justifyContentBetween,
                        classes.mt7
                      )}
                    >
                      <div className={classNames(classes.socialLoginItem)}>
                        <GoogleConnect config={config.google} onlyIcon />
                      </div>
                      <div className={classNames(classes.socialLoginItem)}>
                        <FbConnect config={config.fb} onlyIcon />
                      </div>
                      {/* <div className={classNames(classes.socialLoginItem)}>

                                            </div> */}
                    </div>

                    <div
                      className={classNames(
                        classes.dFlex,
                        classes.w100,
                        classes.my7
                      )}
                    >
                      <div className={classNames(classes.loginSeparator)}></div>
                      <div
                        className={classNames(
                          classes.textMono,
                          classes.textM,
                          classes.textGray300,
                          classes.px3
                        )}
                      >
                        {translate("loginOrContinueThrough")}
                      </div>
                      <div className={classNames(classes.loginSeparator)}></div>
                    </div>

                    <form className={classes.form} onSubmit={this.handleSubmit}>
                      <TextField
                        onChange={this.handleChange}
                        fullWidth
                        required
                        value={username}
                        autoComplete="username"
                        field="username"
                        disabled={disabled}
                      />

                      <TextField
                        onChange={this.handleChange}
                        fullWidth
                        required
                        value={password}
                        autoComplete="password"
                        field="password"
                        disabled={disabled}
                        type={"password"}
                        className={classNames(classes.mt3)}
                      />

                      <div
                        className={classNames(
                          classes.dFlex,
                          classes.justifyContentEnd,
                          classes.mt2
                        )}
                      >
                        <div
                          className={classNames(
                            classes.textMono,
                            classes.uppercase,
                            classes.underline,
                            classes.textGray300,
                            classes.fontSemibold,
                            classes.cursorPointer,
                          )}
                          onClick={this.handleClickReset}
                        >
                          {translate("qForgotPassword")}
                        </div>
                      </div>

                      <Button
                        className={classNames(classes.mt5, classes.loginBtn)}
                        fullwidth
                        arrow
                        type="submit"
                      >
                        {translate("signIn")}
                      </Button>

                      <div
                        className={classNames(
                          classes.dFlex,
                          classes.justifyContentCenter,
                          classes.mt4
                        )}
                      >
                        {translate("loginOr")}
                        <span
                          className={classNames(
                            classes.textMono,
                            classes.uppercase,
                            classes.underline,
                            classes.textPrimary,
                            classes.fontBold,
                            classes.ml2,
                            classes.cursorPointer
                          )}
                          onClick={this.goToRegister}
                        >
                          {translate("loginRegister")}
                        </span>
                      </div>
                    </form>
                  </div>
                </Grid>
              </Grid>
            </div>
          </Grid>

          <Grid
            item
            xs={12}
            sm={5}
            md={6}
            className={classNames(
              classes.dNone,
              classes.dSmBlock,
              classes.loginBg
            )}
          ></Grid>
        </Grid>
      </>
      // <Grid container component="main" className={classnames(classes.root)}>
      //     <CssBaseline />
      //     <Grid item xs={false} sm={4} md={7} className={classes.image} />
      //     <Grid item xs={12} sm={8} md={5} component={"div"} elevation={6} square>
      //         <div className={classes.paper}>
      //             <img alt="logo" className={classes.avatar} src={`/img/${config.locale}/logo.png`} />
      //             {/* <Typography component="h1" variant="h5">
      //                 Sign in
      //             </Typography> */}

      //             <GithubButton fullWidth disabled={disabled} title={translate("githubSignIn")} config={config.github} />
      //             <GoogleButton fullWidth disabled={disabled} title={translate("googleSignIn")} config={config.google} />
      //             <FbButton fullWidth disabled={disabled} title={translate("fbSignIn")} config={config.fb} />
      //             <StravaButton fullWidth disabled={disabled} title={translate("stravaSignIn")} config={config.strava} />

      //             <Typography variant="body1">{translate("useAccount")}</Typography>

      //             <form className={classes.form} onSubmit={this.handleSubmit}>
      //                 <FormControl margin="normal" required fullWidth>
      //                     <InputLabel htmlFor="username">{translate("field.username")}</InputLabel>
      //                     <Input
      //                         id="username"
      //                         name="username"
      //                         autoComplete="username"
      //                         autoFocus
      //                         value={username}
      //                         onChange={this.handleChange}
      //                     />
      //                 </FormControl>
      //                 <FormControl margin="normal" required fullWidth>
      //                     <InputLabel htmlFor="password">{translate("field.password")}</InputLabel>
      //                     <Input
      //                         name="password"
      //                         type="password"
      //                         id="password"
      //                         autoComplete="current-password"
      //                         value={password}
      //                         onChange={this.handleChange}
      //                     />
      //                 </FormControl>

      //                 <Link color="textSecondary" onClick={this.handleClickReset}>
      //                     {translate("qForgotPassword")}
      //                 </Link>

      //                 {config["2fa"] && (
      //                     <FormControl margin="normal" fullWidth>
      //                         <InputLabel htmlFor="password">{translate("field.token")}</InputLabel>
      //                         <Input name="token" id="token" value={token} onChange={this.handleChange} />
      //                     </FormControl>
      //                 )}
      //                 {/* <FormControlLabel
      //   control={<Checkbox value="remember" color="primary" />}
      //   label="Remember me"
      // /> */}
      //                 <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit} disabled={disabled}>
      //                     {translate("signIn")}
      //                 </Button>

      //                 <Link color="textSecondary" onClick={this.handleRegister}>
      //                     {translate("orRegister")}
      //                 </Link>

      //                 {/* <Grid container>
      //     <Grid item xs>
      //       <Link href="#" variant="body2" onClick={this.handleClickReset}>
      //         Forgot password?
      //       </Link>
      //     </Grid>
      //     <Grid item>
      //       <Link  variant="body2">
      //         {"Don't have an account? Sign Up"}
      //       </Link>
      //     </Grid>
      //   </Grid> */}
      //             </form>
      //         </div>
      //         <Copyright />
      //     </Grid>
      // </Grid>
    );
  }
}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withUser,
  withStore,
  withLocalization,
  withRouter
)(Login);
