import React from "react";
import { withStyles } from "@material-ui/core/styles";
import classNames from "classnames";
import compose from "../../../compose";

const styles = (theme) => ({
    icon: {
        height: "20px",
        width: "auto",
    },
});

function TShirtIcon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                width="100%"
                height="100%"
                viewBox="0 0 32 32"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                className={classNames(classes.icon, props.className)}
                style={{ height: `${props.size}px` }}>
                <path
                    fill="currentColor"
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M21.5 13.5V26h-11V13.5l-3.5 2L4.5 10l7-4 2 1.5L16 8l2.5-.5 2-1.5 7 4-2.5 5.5-3.5-2z"></path>
            </svg>
        </>
    );
}

export default compose(withStyles(styles))(TShirtIcon);
