import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import withUser from "../../../consumers/withUser";
import { withLocalization } from "../../shared/localization";
import withStore from "../../../consumers/withStore";
import withDoby from "../../../consumers/withDoby";
import withUtils from "../../shared/utils/withUtils";
import compose from "../../../compose";
import getSharedStyles from "../../../style";
import classNames from "classnames";

import Button from "../../dobyComponents/form/Button";
import Switch from "../../dobyComponents/form/Switch";

import TextField from "../../dobyComponents/form/TextField";
import TelField from "../../dobyComponents/form/TelField";
import SelectField from "../../dobyComponents/form/SelectField";
// import Checkbox from "../../dobyComponents/form/CheckboxField";

import FbConnect from "../../dobyComponents/socialConnections/login/FacebookConnection";
import GoogleConnect from "../../dobyComponents/socialConnections/login/GoogleConnection";

import PageHeader from "../../dobyComponents/PageHeader";

import _ from "lodash";
import config from "../../../config";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    profileImgHolder: {
        width: "120px",
        height: "120px",
        minWidth: "120px",
        overflow: "hidden",
        position: "relative",
    },
    profileImg: {
        borderRadius: "100px",
        border: "3px solid white",
        width: "100%",
        height: "100%",
        position: "absolute",
        left: 0,
        right: 0,
        objectFit: "cover",
    },
    avatar: {},
    googleLogin: {
        border: `1px solid #C8D2E1`
    }
});

const INIT_STATE = {
    password: {
        value: "",
        errorText: "",
    },
    repeatedPassword: {
        value: "",
        errorText: "",
    },
    email: {
        value: "",
        isLoading: false,
        errorText: "",
    },
    name: "",
    username: {
        value: "",
        isLoading: false,
        errorText: "",
    },
    tel: "",
    address: "",
    city: "",
    postCode: "",
    countryId: "",
    otp: false,
    avatar: null,
    notifications: true,
    // notifyAboutMe: false,
    // notifyAboutFollowers: false,
    // notifyNews: false,
};

const buildState = (user) => {
    return {
        ...INIT_STATE,
        ...user,
        email: { ...INIT_STATE.email, value: user.email },
        username: { ...INIT_STATE.username, value: user.username },
        isOpenSecretDialog: false,
        avatarChanged: false,
    };
};

class ProfileSettings extends React.Component {
    constructor(props) {
        super(props);
        this.state = buildState(this.props.user.user);
    }

    async componentDidMount() {
        const { user } = this.props.doby;

        await this.fetchData();

        const hasDisableNotifications = user && user.meta && user.meta.disableNotifications && user.meta.disableNotifications === "1";
        this.setState({ notifications: !hasDisableNotifications });
    }

    fetchData = async () => {
        const { getEnums } = this.props.store;
        const filter = [
            { field: "enumTypeId", value: 5 },
            { field: "disabled", value: false },
        ];
        await getEnums(filter);
    };

    handleChange = (name, value) => {
        this.setState((state) => _.set(state, name, value));
    };

    handleChangeCountry = (name, value) => {
        const { enums } = this.props.store;

        const found = enums.data.find((e) => e._id === value);
        this.setState({ [name]: found ? found.key : 0 });
    };

    handleChangeCheckBox = (event) => {
        const { name, checked } = event.target;
        this.setState((state) => _.set(state, name, checked));
    };

    handleChangeAvatar = (event) => {
        event.preventDefault();
        const reader = new FileReader();
        reader.onloadend = async () => {
            let data = await this.props.utils.resize(reader.result, config.avatarSize);
            data = data.split(",").pop();
            this.setState({ avatar: data, avatarChanged: true });
        };
        reader.readAsDataURL(event.target.files[0]);

        // zabezpecuje volanie onChange aj pri uploadovani rovnakeho suboru
        // -> po ulozeni formularu nezavolalo onChange opat, v pripade nahrania
        // -> rovnakeho suboru ako predtym
        event.target.value = "";
    };

    handleRemoveAvatar = () => {
        this.setState({ avatar: null, avatarChanged: true });
    };

    validEmail = async () => {
        const { translate } = this.props;
        const { email } = this.state;

        this.setState((state) => ({
            email: {
                ...state.email,
                isLoading: true,
            },
        }));

        const isValid = !email.value || (await this.isValid(email.value));
        const errorText = isValid ? "" : translate("field.emailInUse");

        this.setState((state) => ({
            email: { ...state.email, errorText, isLoading: false },
        }));

        return isValid;
    };

    isValid = async (username) => {
        const { user } = this.props;

        const ids = await this.props.user.checkUser(username);
        return ids.length === 0 || (ids.length === 1 && ids[0]._id === user.user._id);
    };

    validUsername = async () => {
        const { translate } = this.props;
        const { username } = this.state;

        this.setState((state) => ({
            username: {
                ...state.username,
                isLoading: true,
            },
        }));

        const isValid = !username.value || (await this.isValid(username.value));
        const errorText = isValid ? "" : translate("field.usernameInUse");

        this.setState((state) => ({
            username: { ...state.username, errorText, isLoading: false },
        }));

        return isValid;
    };

    handleSubmit = async () => {
        const { password, name, username, email, tel, address, city, postCode, countryId, otp, avatar, avatarChanged, notifications } = this.state;

        const isValid = (await this.validEmail()) && (await this.validUsername());
        if (!isValid) {
            return;
        }

        try {
            await this.props.user.updateUser(this.state._id, {
                email: email.value,
                password: password.value,
                name,
                username: username.value,
                tel,
                address,
                city,
                postCode,
                countryId,
                otp,
                avatar: avatarChanged ? avatar : null,
            });
            await this.props.doby.postMeta(`key=disableNotifications&value=${!notifications ? 1 : 0}`);

            const stateBuilded = buildState(this.props.user.user);
            this.setState({ ...stateBuilded, notifications });
            this.props.user.showMsg("Ok.");
        } catch (error) {
            this.props.user.showError(error);
        }
    };

    goToProfile = () => this.props.history.push(`/profile`);

    render() {
        const { classes, translate, disabled } = this.props;
        const { enums } = this.props.store;
        const {
            email,
            tel,
            address,
            city,
            postCode,
            countryId,
            avatar,
            name,
            bio,
            notifications,
            // notifyAboutMe,
            // notifyAboutFollowers,
            // notifyNews,
        } = this.state;

        const country = enums.data.find((e) => e.key === countryId);
        return (
            <>
                <PageHeader title={this.props.translate("navigation.settings")} />

                <div className={classNames(classes.mt5)}>
                    <Grid container justify="center">
                        <Grid item xs={12} sm={10} md={8} lg={6}>
                            <Grid container direction="row" justify="center" alignItems="center">
                                <Grid item xs={12}>
                                    <div className={classNames(classes.container, classes.mt2)}>
                                        <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.w100, classes.mt3)}>
                                            <div className={classNames(classes.profileImgHolder)}>
                                                <img
                                                    alt="avatarImg"
                                                    src={avatar ? "data:image/jpeg;base64," + avatar : "./img/user-img.jpg"}
                                                    className={classNames(classes.profileImg)}
                                                />
                                            </div>

                                            <div className={classNames(classes.flexCol, classes.flexGrow, classes.ml5)}>
                                                <div className={classNames(classes.textXs2)}>{translate("descSettingsProfilePhoto")}</div>
                                                <div className={classNames(classes.mt3, classes.w100)}>
                                                    <Button onClick={() => {}} fullwidth>
                                                        <label for="avatar-upload">{translate("changePhoto")}</label>
                                                        <input
                                                            hidden
                                                            type="file"
                                                            accept="image/jpeg"
                                                            onChange={this.handleChangeAvatar}
                                                            id="avatar-upload"
                                                        />
                                                    </Button>
                                                </div>
                                                <div className={classNames(classes.mt3, classes.w100)}>
                                                    <Button fullwidth outline onClick={this.handleRemoveAvatar}>
                                                        {translate("removePhoto")}
                                                    </Button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Grid>

                                <Grid item xs={12} className={classNames(classes.mt3)}>
                                    <div className={classNames(classes.container)}>
                                        <div className={classNames(classes.dFlex, classes.flexCol, classes.w100, classes.justifyContentBetween, classes.mt7)}>
                                            <div className={classNames(classes.googleLogin)}>
                                                <GoogleConnect config={config.google} />
                                            </div>
                                            <div className={classNames(classes.mt2)}>
                                                <FbConnect config={config.fb} />
                                            </div>
                                        </div>
                                        {/* <FacebookConnection config={config.fb} /> */}
                                        {/* <StravaConnection
                                config={config.strava}
                                className={classNames(classes.mt3)}
                                /> */}
                                        {/* <GoogleConnection config={config.google} /> */}
                                    </div>
                                </Grid>

                                <Grid item xs={12} className={classNames(classes.mt6)}>
                                    <div className={classNames(classes.container)}>
                                        <div className={classNames(classes.fontBold, classes.textMonoH4)}>{translate("personData")}</div>

                                        <Grid container direction="row" justify="center" alignItems="center">
                                            <Grid item xs={12} className={classNames(classes.mt4, classes.pr1)}>
                                                <TextField
                                                    onChange={this.handleChange}
                                                    fullWidth
                                                    required
                                                    value={name}
                                                    field="name"
                                                    disabled={disabled}
                                                />
                                            </Grid>
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <TextField
                                                    onChange={this.handleChange}
                                                    fullWidth
                                                    required
                                                    value={bio}
                                                    field="bio"
                                                    rows={3}
                                                    multiline
                                                    disabled={disabled}
                                                />
                                            </Grid>
                                        </Grid>

                                        <div className={classNames(classes.fontBold, classes.textMonoH4, classes.mt6)}>
                                            {translate("delivery_details")}
                                        </div>

                                        <Grid container direction="row" justify="center" alignItems="center">
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <TextField
                                                    onChange={this.handleChange}
                                                    fullWidth
                                                    required
                                                    value={email.value}
                                                    field="email.value"
                                                    type="email"
                                                    disabled={disabled || email.isLoading}
                                                    onBlur={this.validEmail}
                                                    errorText={email.errorText}
                                                />
                                            </Grid>
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <TelField
                                                    fullWidth
                                                    value={tel}
                                                    field="tel"
                                                    onChange={this.handleChange}
                                                    disabled={disabled}
                                                    // errorText={tel.errorText}
                                                />
                                            </Grid>
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <SelectField
                                                    onChange={this.handleChangeCountry}
                                                    fullWidth
                                                    data={enums.data}
                                                    value={country ? country._id : ""}
                                                    field="countryId"
                                                    textField="name"
                                                    disabled={disabled}
                                                />
                                            </Grid>
                                            <Grid item xs={8} className={classNames(classes.mt4, classes.pr2)}>
                                                <TextField
                                                    fullWidth
                                                    value={city}
                                                    field="city"
                                                    type="city"
                                                    onChange={this.handleChange}
                                                    disabled={disabled}
                                                />
                                            </Grid>
                                            <Grid item xs={4} className={classNames(classes.mt4)}>
                                                <TextField
                                                    fullWidth
                                                    value={postCode}
                                                    field="postCode"
                                                    type="postCode"
                                                    onChange={this.handleChange}
                                                    disabled={disabled}
                                                />
                                            </Grid>
                                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                                <TextField
                                                    fullWidth
                                                    value={address}
                                                    field="address"
                                                    type="address"
                                                    onChange={this.handleChange}
                                                    disabled={disabled}
                                                />
                                            </Grid>
                                        </Grid>
                                    </div>
                                </Grid>

                                <Grid item xs={12} className={classNames(classes.mt6)}>
                                    <div className={classNames(classes.container)}>
                                        <div
                                            className={classNames(
                                                classes.dFlex,
                                                classes.w100,
                                                classes.alignItemsCenter,
                                                classes.justifyContentBetween
                                            )}>
                                            <div className={classNames(classes.fontBold)}>{translate("notifications")}</div>
                                            <div>
                                                <Switch
                                                    value={notifications}
                                                    field="notifications"
                                                    onChange={this.handleChange}
                                                    //disabled={disabled}
                                                />
                                            </div>
                                        </div>

                                        {/* <div className={classNames(classes.dBlock)}>
                                <Checkbox
                                    onChange={this.handleChange}
                                    fullWidth
                                    required
                                    value={notifyAboutMe}
                                    field="notifyAboutMe"
                                    label={translate("notifyAboutMe")}
                                    disabled={!notifications || disabled}
                                />
                                </div>

                                <div className={classNames(classes.dBlock)}>
                                <Checkbox
                                    onChange={this.handleChange}
                                    fullWidth
                                    required
                                    value={notifyAboutFollowers}
                                    field="notifyAboutFollowers"
                                    label={translate("notifyAboutFollowers")}
                                    disabled={!notifications || disabled}
                                />
                                </div>

                                <div className={classNames(classes.dBlock)}>
                                <Checkbox
                                    onChange={this.handleChange}
                                    fullWidth
                                    required
                                    value={notifyNews}
                                    field="notifyNews"
                                    label={translate("notifyNews")}
                                    disabled={!notifications || disabled}
                                />
                                </div> */}
                                    </div>
                                </Grid>

                                <Grid item xs={12} className={classNames(classes.mt3, classes.mb5)}>
                                    <div className={classNames(classes.container)}>
                                        <div className={classNames(classes.dFlex, classes.justifyContentEnd)}>
                                            <Button fullwidth onClick={this.handleSubmit}>
                                                {translate("saveSettings")}
                                            </Button>
                                        </div>
                                    </div>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </div>
            </>
        );
    }
}

ProfileSettings.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(withStyles(styles), withUser, withLocalization, withStore, withDoby, withUtils)(ProfileSettings);
