            months = moment(challenge.end).diff(moment(challenge.start), "months", true);
            months = Math.abs(months);
            months = Math.ceil(months);
            start = moment(challenge.start).format("DD.MM");
            end = moment(challenge.end).format("DD.MM.YYYY");
