import React from "react";
import { withStyles } from "@material-ui/core/styles";
import compose from "../../../compose";
import { withLocalization } from "../../shared/localization";
import withStore from "../../../consumers/withStore";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import config from "../../../config";

import classNames from "classnames";
import getSharedStyles from "../../../style";

import TextField from "../../dobyComponents/form/TextField";
import Checkbox from "../../dobyComponents/form/CheckboxField";
import Button from "../../dobyComponents/form/Button";
import CheckoutStepper from "../../dobyComponents/CheckoutStepper";
import CartItemCheckout from "../../dobyComponents/eshop/CartItemCheckout";

import Grid from "@material-ui/core/Grid";
//import CheckouCartItem from './CheckouCartItem';

import { loadStripe } from "@stripe/stripe-js";
import { formatPrice, getCurrencySymbol } from "../../../utils/helpers";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  starterPackDiscount: {
    border: "1px solid #C8D2E1",
    background: "#ECECEC",
  },
  fixedBottomBar: {
    position: "fixed",
    bottom: "0",
    left: "0",
    width: "100%",
    background: theme.palette.secondary.main,
    zIndex: 1,
  },
  spacingBottom: {
    paddingBottom: "190px",
  },
});

class CheckoutSummary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      starterPackDiscount: false,
      discountCode: "",
      standardPack: null,
      standardPackProduct: null,
      discountError: null,
      gdprFirst: false,
      gdprSecond: false,
      challengeAlreadyBoughtError: null,
    };
  }

  componentDidMount() {
    this.props.doby.cartGetProducts();
    this.fetchData();
    this.checkStarterpack();
    this.checkAlreadyBought();
  }

  checkAlreadyBought = async () => {
    const { doby, user } = this.props;
    await doby.getUser(user.user._id);
    const dobyUser = this.props.doby.user;

    let currentChallengeId = 0;
    const { cart } = this.props.doby;

    for (let item of cart.data.products) {
      if (item.product.catalog_id === "pack") {
        currentChallengeId = item.product.challengeRef.id;
        break;
      }
    }

    const currentChallengeWasAlreadyBought =
      dobyUser &&
      dobyUser.challengesRef &&
      dobyUser.challengesRef.find((x) => x.id === currentChallengeId);
    this.setState({
      challengeAlreadyBoughtError: currentChallengeWasAlreadyBought
        ? this.props.translate("challange_already_bough")
        : null,
    });
  };

  fetchData = async () => {
    const { getEnums } = this.props.store;
    const filter = [
      { field: "enumTypeId", value: 5 },
      { field: "disabled", value: false },
    ];

    await getEnums(filter);
    await this.props.store.getTransports();
  };

  handleChange = async (field, value) => {
    this.setState({
      [field]: value,
    });

    if (field === "discountCode") {
      if (value.length === config.discountCodeLength) {
        let coupon = await this.props.doby.redeeemDiscountCode(value);
        if (!coupon) {
          this.setState({
            discountError: this.props.translate("coupon_error_unknown"),
          });
        } else {
          this.setState({ discountError: null });
        }
      } else {
        this.setState({
          discountError: this.props.translate("coupon_error_short"),
        });
      }
    }
  };

  checkStarterpack = async () => {
    const { cart } = this.props.doby;

    for (let item of cart.data.products) {
      if (item.product.catalog_id === "pack") {
        const filter = [
          {
            field: "_id",
            value: item.product.productsRef.map((item) => item.id),
            op: "in",
          },
          { field: "disabled", value: false },
        ];
        let products = await this.props.doby.getProducts(filter);
        for (let packProduct of products.data) {
          if (packProduct.catalog_id === "coupon") {
            this.setState({
              standardPack: packProduct,
              standardPackProduct: item.product,
            });
          }
        }
      }
    }
  };

  renderCartitems() {
    const { cart } = this.props.doby;
    return cart.data.products.map((item) => {
      const { product } = item;
      return (
        <CartItemCheckout
          key={product._id}
          productId={product._id}
          productName={product.name}
          priceWithTax={product.price_with_tax}
          thumbnailId={product.thumbnailId}
          productCount={item.count}
          countInStock={product.count}
          catalogId={product.catalog_id}
          challengeRef={product.challengeRef && product.challengeRef.id}
        />
        // <CheckouCartItem
        //     key={product.id}
        //     productId={product.id}
        //     productName={product.name}
        //     priceWithTax={product.price_with_tax}
        //     thumbnailId={product.thumbnailId}
        //     productCount={item.count}
        //     countInStock={product.count}
        // //techParams={product.techParams}
        // />
      );
    });
  }

  getSumOfTaxPrice = () => {
    const { products } = this.props.doby.cart.data;
    const { transports } = this.props.store;
    const {
      deliveryType,
      paymentMethods,
      paymentMethod,
    } = this.props.doby.checkout.data.deliveryData;

    let taxPrice = 0;
    for (const item of products) {
      if (item.product.catalog_id === "pack")
        taxPrice +=
          item.product.price_with_tax - item.product.price_without_tax;
      else
        taxPrice +=
          item.count *
          (item.product.price_with_tax - item.product.price_without_tax);
    }

    //NOTE: add payment method price
    const paymentMethodEntire = paymentMethods.find(
      (x) => x.id === paymentMethod
    );
    if (paymentMethodEntire) {
      taxPrice += this.getTaxPrice(paymentMethodEntire.price);
    }

    //NOTE: add delivery price
    const transport = transports.data.find((x) => x._id === deliveryType);
    if (transport) {
      taxPrice += this.getTaxPrice(transport.price);
    }

    return formatPrice(taxPrice);
  };

  getTaxPrice = (priceWithTax) => {
    const priceTax = (config.tax / 100) * priceWithTax;
    return Math.round(priceTax, 2);
  };

  getTotalPrice = () => {
    const { transports } = this.props.store;
    const { coupon } = this.props.doby.checkout.data;
    const {
      deliveryType,
      paymentMethods,
      paymentMethod,
    } = this.props.doby.checkout.data.deliveryData;
    const { products } = this.props.doby.cart.data;

    let priceProducts = 0;
    let pricePack = 0;
    let price = 0;

    for (const item of products) {
      if (item.product.catalog_id === "pack")
        pricePack += item.product.price_with_tax;
      else priceProducts += item.count * item.product.price_with_tax;
    }

    //NOTE: apply discounts, before adding price for delivery
    if (coupon._id) {
      //TODO: enums ?
      if (coupon.discountType === 1) {
        price = (price * (100.0 - coupon.amount)) / 100.0;
      } else {
        price -= coupon.amount;
      }
    }

    let couponBalance = this.state.standardPack
      ? this.state.standardPack.price_with_tax
      : 0;
    if (!this.state.starterPackDiscount) {
      price = priceProducts + pricePack;
    } else {
      if (priceProducts < couponBalance) {
        priceProducts = 0;
        couponBalance -= priceProducts;
      } else {
        priceProducts = priceProducts - couponBalance;
        couponBalance = 0;
      }

      price = priceProducts + pricePack;
    }

    //NOTE: add payment method price
    const paymentMethodEntire = paymentMethods.find(
      (x) => x.id === paymentMethod
    );
    if (paymentMethodEntire) {
      price += paymentMethodEntire.price;
    }

    //NOTE: add delivery price
    const transport = transports.data.find((x) => x._id === deliveryType);
    if (transport) {
      price += transport.price;
    }

    return formatPrice(price);
  };

  proceedCheckout = async () => {
    const { cart } = this.props.doby;
    const { deliveryData, coupon } = this.props.doby.checkout.data;

    let items = [];
    for (const product of cart.data.products) {
      product.product.count = product.count;
      if (
        this.state.standardPackProduct &&
        product.product._id === this.state.standardPackProduct._id
      ) {
        items.push({
          ...product.product,
          id: product.product._id,
          useCoupon: this.state.starterPackDiscount,
        });
        continue;
      }

      items.push({ ...product.product, id: product.product._id });
    }

    //TODO: validation ( function ? )
    const name = deliveryData.nameSurname.split(" ")[0];
    const surname = deliveryData.nameSurname.split(" ")[1];
    const country_obj = this.props.store.enums.data.find(
      (x) => x._id === deliveryData.country
    );
    
    const id_delivery_obj = this.props.store.transports.data.find(
      (x) => x._id === deliveryData.deliveryType
    );

    if (!country_obj) return;

    const destination_country_id = country_obj.key;
    const id_delivery = id_delivery_obj ? id_delivery_obj.key : 0;

    const order = await this.props.doby.createOrder({
      //---
      name,
      surname,
      phone: deliveryData.tel,
      email: deliveryData.email,
      street: deliveryData.street,
      street_number: deliveryData.streetNumber,
      postal_code: deliveryData.psc,
      city: deliveryData.city,
      country: deliveryData.country,
      couponRef: { ...coupon, id: coupon._id },

      //--- FA
      fa_city: deliveryData.invoiceCity,
      fa_company: "",
      fa_country: deliveryData.invoiceCountry,
      fa_postal_code: deliveryData.invoicePsc,
      fa_street: deliveryData.invoiceStreet,
      fa_street_number: deliveryData.invoiceStreet,
      //---
      fa_ico: deliveryData.ico,
      fa_dic: deliveryData.dic,
      tax_reverse: deliveryData.VATPayer,
      fa_icpdh: deliveryData.icdph,

      id_delivery,
      payment_method_id: deliveryData.paymentMethod,
      items,
      destination_country_id,
      ru: process.env.REACT_APP_HOMEPAGE,
    });

    if (order) {
      if (deliveryData.paymentMethod === 8) {
        this.props.history.push(`/eshop/checkout/verify?success=true`);
        return;
      }

      const { payment_id } = order;
      const stripe = await loadStripe(config.stripe.id);

      const result = await stripe.redirectToCheckout({
        sessionId: payment_id,
      });

      if (result.error) {
        // If `redirectToCheckout` fails due to a browser or network
        // error, display the localized error message to your customer
        // using `result.error.message`.
        console.error(result.error);
        this.props.history.push(`/eshop/checkout/verify?success=false`);
      }
    } else {
      this.props.history.push(`/eshop/checkout/verify?success=false`);
    }
  };

  renderDiscounts = () => {
    const { classes } = this.props;
    const { coupon } = this.props.doby.checkout.data;
    const getDiscountByType = (type, amount) =>
      type === 1
        ? `-${amount}%`
        : `-${amount}${getCurrencySymbol(config.currency)}`;
    return (
      <>
        {coupon._id && (
          <div
            className={classNames(
              classes.dFlex,
              classes.alignItemsCenter,
              classes.justifyContentBetween,
              classes.w100
            )}
          >
            <div className={classNames(classes.textXs)}>
              {this.props.translate("coupon_discount")}
            </div>
            <div className={classNames(classes.textXs)}>
              {getDiscountByType(coupon.discountType, coupon.amount)}
            </div>
          </div>
        )}
      </>
    );
  };

  renderStarterpackDiscount = () => {
    const { classes } = this.props;

    return (
      this.state.standardPack && (
        <Grid item xs={12} className={classes.mt3}>
          <div
            className={classNames(
              classes.starterPackDiscount,
              classes.px4,
              classes.py2,
              classes.roundedMd,
              classes.dFlex,
              classes.alignItemsCenter
            )}
          >
            <div>
              <Checkbox
                onChange={this.handleChange}
                fullWidth
                required
                value={this.state.starterPackDiscount}
                field="starterPackDiscount"
                noLabel
              />
            </div>
            <div className={classNames(classes.flexCol)}>
              <div className={classNames(classes.textXs)}>
                {this.props.translate("starterpack_discount")}
              </div>
              <div className={classNames(classes.textXs2)}>
                - {formatPrice(this.state.standardPack.price_with_tax)} na
                produkty
              </div>
            </div>
          </div>
        </Grid>
      )
    );
  };

  render() {
    const { classes, user } = this.props;
    const { deliveryData } = this.props.doby.checkout.data;
    const country = this.props.store.enums.data.find(
      (x) => x._id === deliveryData.country
    );
    return (
      <>
        <CheckoutStepper className={classNames(classes.mt5)} currentState={4} />

        <div className={classNames(classes.bgLight)}>
          <div className={classNames(classes.container, classes.spacingBottom)}>
            <Grid container direction="row" justify="space-between">
              <Grid item xs={12} md={5} lg={4} className={classNames()}>
                <h3
                  className={classNames(
                    classes.uppercase,
                    classes.mt7,
                    classes.mb4,
                    classes.dMdNone
                  )}
                >
                  {this.props.translate("stepper.summary")}
                </h3>
                <h4
                  className={classNames(
                    classes.uppercase,
                    classes.mt7,
                    classes.mb4,
                    classes.dNone,
                    classes.dMdBlock
                  )}
                >
                  {this.props.translate("stepper.summary")}
                </h4>

                <div className={classNames(classes.flexCol, classes.w100)}>
                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.flexCol,
                      classes.flexXsRow,
                      classes.w100,
                      classes.justifyContentXsBetween,
                      classes.alignItemsXsCenter
                    )}
                  >
                    <div
                      className={classNames(
                        classes.textMono,
                        classes.fontSemibold
                      )}
                    >
                      {this.props.translate("field.name")}
                    </div>
                    <div className={classNames(classes.mt1, classes.mtXs0)}>
                      {deliveryData.nameSurname}
                    </div>
                  </div>
                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.flexCol,
                      classes.flexXsRow,
                      classes.w100,
                      classes.justifyContentXsBetween,
                      classes.alignItemsXsCenter,
                      classes.mt3
                    )}
                  >
                    <div
                      className={classNames(
                        classes.textMono,
                        classes.fontSemibold
                      )}
                    >
                      {this.props.translate("field.email")}
                    </div>
                    <div className={classNames(classes.mt1, classes.mtXs0)}>
                      {deliveryData.email}
                    </div>
                  </div>
                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.flexCol,
                      classes.flexXsRow,
                      classes.w100,
                      classes.justifyContentXsBetween,
                      classes.alignItemsXsCenter,
                      classes.mt3
                    )}
                  >
                    <div
                      className={classNames(
                        classes.textMono,
                        classes.fontSemibold
                      )}
                    >
                      {this.props.translate("field.tel")}
                    </div>
                    <div className={classNames(classes.mt1, classes.mtXs0)}>
                      {`+${deliveryData.tel}`}
                    </div>
                  </div>

                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.flexCol,
                      classes.flexXsRow,
                      classes.w100,
                      classes.justifyContentXsBetween,
                      classes.mt6,
                      classes.mtSm7
                    )}
                  >
                    <div
                      className={classNames(
                        classes.textMono,
                        classes.fontSemibold
                      )}
                    >
                      {this.props.translate("delivery_address")}
                    </div>
                    <div
                      className={classNames(
                        classes.flexCol,
                        classes.textXsRight,
                        classes.mt1,
                        classes.mtXs0
                      )}
                    >
                      <div className={classNames()}>{deliveryData.street}</div>
                      <div className={classNames(classes.mt1)}>
                        {`${deliveryData.email}, ${deliveryData.psc}`}
                      </div>
                      <div className={classNames(classes.mt1)}>
                        {country ? country.name : ""}
                      </div>
                      <div
                        className={classNames(
                          classes.mt2,
                          classes.fontSemibold,
                          classes.uppercase,
                          classes.underline,
                          classes.textM
                        )}
                        onClick={() => {
                          this.props.history.push(
                            `/eshop/checkout-delivery-payment`
                          );
                        }}
                      >
                        {this.props.translate("edit")}
                      </div>
                    </div>
                  </div>
                </div>
              </Grid>

              <Grid item xs={12} md={6} className={classes.mt8}>
                <h4 className={classNames(classes.uppercase, classes.mb4)}>
                  {this.props.translate("cart")}
                </h4>
                {/* <hr className={classNames(classes.w100, classes.mb4, classes.mt4, classes.hrLine)} /> */}
                <div className={classNames()}>{this.renderCartitems()}</div>

                {/* <hr className={classNames(classes.w100, classes.mb4, classes.hrLine)} /> */}
                <div className={classNames(classes.mt8)}>
                  <TextField
                    onChange={this.handleChange}
                    fullWidth
                    value={this.state.discountCode}
                    field="discountCode"
                    errorText={this.state.discountError}
                  />
                </div>

                <div className={classNames(classes.mt6)}>
                  <div className={classNames()}>
                    <Checkbox
                      onChange={this.handleChange}
                      fullWidth
                      required
                      value={this.state.gdprFirst}
                      field="gdprFirst"
                    />
                  </div>

                  <div className={classNames(classes.mt2)}>
                    <Checkbox
                      onChange={this.handleChange}
                      fullWidth
                      required
                      value={this.state.gdprSecond}
                      field="gdprSecond"
                    />
                  </div>
                </div>

                <div
                  className={classNames(
                    classes.mt6,
                    classes.dFlex,
                    classes.w100,
                    classes.justifyContentCenter
                  )}
                >
                  <span style={{ color: "red" }}>
                    {this.state.challengeAlreadyBoughtError}
                  </span>
                </div>

                <div
                  className={classNames(
                    classes.mt8,
                    classes.dNone,
                    classes.dMdBlock
                  )}
                >
                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.mt2,
                      classes.justifyContentBetween
                    )}
                  >
                    <div className={classNames(classes.flexCol)}>
                      <div
                        className={classNames(
                          classes.textMono,
                          classes.fontSemibold
                        )}
                      >
                        {this.props.translate("totalPrice")}
                      </div>
                      <div
                        className={classNames(
                          classes.textGray400,
                          classes.textM,
                          classes.mt1
                        )}
                      >
                        {this.props.translate("totalTax")}
                      </div>
                    </div>
                    <div
                      className={classNames(
                        classes.textMono,
                        classes.fontSemibold,
                        classes.textRight
                      )}
                    >
                      {this.getTotalPrice()}

                      <div
                        className={classNames(
                          classes.textGray400,
                          classes.textM,
                          classes.mt1
                        )}
                      >
                        {this.getSumOfTaxPrice()}
                      </div>
                    </div>
                  </div>
                  <div
                    className={classNames(
                      classes.dFlex,
                      classes.w100,
                      classes.justifyContentEnd
                    )}
                  >
                    <Button
                      onClick={this.proceedCheckout}
                      fullwidth
                      className={classNames(classes.mt5)}
                      arrow
                      disabled={!this.state.gdprFirst || !this.state.gdprSecond}
                      style={{ maxWidth: "none" }}
                    >
                      {this.props.translate("continue")}
                    </Button>
                  </div>
                </div>
              </Grid>

              {this.renderStarterpackDiscount()}

              <Grid item xs={12} className={classes.mt6}></Grid>
            </Grid>
          </div>
        </div>

        <div
          className={classNames(
            classes.fixedBottomBar,
            classes.py4,
            classes.dMdNone
          )}
        >
          <div className={classNames(classes.container)}>
            <div className={classNames(classes.flexCol, classes.w100)}>
              {this.renderDiscounts()}

              <div
                className={classNames(
                  classes.dFlex,
                  classes.mt2,
                  classes.justifyContentBetween
                )}
              >
                <div className={classNames(classes.flexCol)}>
                  <div
                    className={classNames(
                      classes.textMono,
                      classes.textWhite,
                      classes.fontSemibold
                    )}
                  >
                    {this.props.translate("totalPrice")}
                  </div>
                  <div
                    className={classNames(
                      classes.textGray200,
                      classes.textM,
                      classes.mt1
                    )}
                  >
                    {this.props.translate("totalTax")}
                  </div>
                </div>
                <div
                  className={classNames(
                    classes.textMono,
                    classes.textWhite,
                    classes.fontSemibold,
                    classes.textRight
                  )}
                >
                  {this.getTotalPrice()}

                  <div
                    className={classNames(
                      classes.textGray200,
                      classes.textM,
                      classes.mt1
                    )}
                  >
                    {this.getSumOfTaxPrice()}
                  </div>
                </div>
              </div>

              <div
                className={classNames(
                  classes.dFlex,
                  classes.w100,
                  classes.justifyContentEnd
                )}
              >
                <Button
                  onClick={this.proceedCheckout}
                  fullwidth
                  className={classNames(classes.mt4)}
                  arrow
                  disabled={
                    !this.state.gdprFirst ||
                    !this.state.gdprSecond ||
                    this.state.challengeAlreadyBoughtError !== null
                  }
                  style={{ maxWidth: "none" }}
                >
                  {this.props.translate("continue")}
                </Button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default compose(
  withStyles(styles),
  withLocalization,
  withStore,
  withDoby,
  withUser
)(CheckoutSummary);
