import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";
import withStore from "../../consumers/withStore";
import withDoby from "../../consumers/withDoby";
import getSharedStyles from "../../style";
import compose from "../../compose";
import classNames from "classnames";
import Button from "./form/Button";
import moment from "moment";

import StopwatchIcon from "../dobyComponents/icons/Stopwatch";
import MountainIcon from "../dobyComponents/icons/Mountain";
import ArrowRight from "../dobyComponents/icons/ArrowRight";
import FlagIcon from "../dobyComponents/icons/Flag";

const styles = (theme) => ({
  ...getSharedStyles(theme),

  card: {
    width: "100%",
    height: "450px",
    position: "relative",
    overflow: "hidden",
  },
  challengeImgHolder: {
    //height: '270px',
    position: "absolute",
    width: "100%",
    height: "100%",
    top: "0",
    left: "0",
    "&:after": {
      content: "''",
      display: "block",
      position: "absolute",
      bottom: "0",
      left: "0",
      width: "100%",
      height: "100%",
      background:
        "linear-gradient(180deg, rgba(0, 20, 31, 0.32) 0%, rgba(2, 16, 25, 0.55) 56.58%, rgba(3, 15, 23, 0.6) 100%)",
    },
  },
  cardContent: {
    zIndex: 10,
    position: "relative",
  },
  challengeImg: {
    position: "center",
    left: "0",
    right: "0",
    width: "100%",
    height: "100%",
    objectFit: "cover",
  },

  logoHolder: {},
  challengeLogo: {
    height: "auto",
    width: "150px",
  },

  difficultyDot: {
    width: "9px",
    height: "9px",
    borderRadius: "10px",
    marginRight: "5px",
    background: "white",
    opacity: "0.4",
  },
  dotFilled: {
    opacity: "1",
  },
  challengeBtn: {},
  challengeMap: {
    height: "35px",
    width: "auto",
  },
});

function ChallengeCard(props) {
  const [image, setImage] = useState(null);
  const [logo, setLogo] = useState(null);
  const [map, setMap] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      if (props.image) {
        const data = await props.doby.getFile(props.image);
        const reader = new FileReader();
        reader.onloadend = () => {
          setImage(reader.result);
        };
        reader.readAsDataURL(data);
      }
      if (props.logo) {
        const data = await props.doby.getFile(props.logo);
        const reader = new FileReader();
        reader.onloadend = () => {
          setLogo(reader.result);
        };
        reader.readAsDataURL(data);
      }
      if (props.map) {
        const data = await props.doby.getFile(props.map);
        const reader = new FileReader();
        reader.onloadend = () => {
          setMap(reader.result);
        };
        reader.readAsDataURL(data);
      }
    };

    fetchData();
  }, [props.doby, props.image, props.logo, props.map]);

  const { classes, difficulty, className, translate, logoCenter } = props;

  const summitsCount = props.summits && props.summits.length;
  //const startDate = moment(props.start).format('DD.MM.YYYY');

  let months = moment(props.end).diff(moment(props.start), "months", true);
  months = Math.abs(months);
  months = Math.ceil(months);

  return (
    <div
      className={classNames(classes.card, className)}
      onClick={props.onClick && props.onClick}
    >
      <div className={classNames(classes.challengeImgHolder)}>
        <img src={image} className={classNames(classes.challengeImg)} alt="" />
      </div>
      <div
        className={classNames(
          classes.dFlex,
          classes.flexCol,
          classes.w100,
          classes.h100,
          classes.justifyContentBetween,
          classes.cardContent
        )}
      >
        <div
          className={classNames(
            classes.p6,
            classes.pXl7,
            classes.dFlex,
            classes.flexCol,
            classes.h100
          )}
        >
          <div
            className={classNames(
              classes.dFlex,
              classes.w100,
              classes.mt3,
              logoCenter && classes.justifyContentCenter
            )}
          >
            <img
              src={logo}
              className={classNames(classes.challengeLogo, classes.mx4)}
              alt=""
            />
          </div>
          <div
            className={classNames(
              classes.dFlex,
              classes.alignItemsCenter,
              classes.mtAuto
            )}
          >
            <MountainIcon size={25} className={classes.textWhite} />
            <h5 className={classNames(classes.textWhite, classes.ml3)}>
              {summitsCount}{" "}
              {summitsCount === 1
                ? translate("challenge.summit")
                : summitsCount < 5
                ? translate("challenge.summits")
                : translate("challenge.summit_gen")}
            </h5>
          </div>
          <div
            className={classNames(
              classes.dFlex,
              classes.alignItemsCenter,
              classes.mt2
            )}
          >
            <StopwatchIcon size={25} className={classes.textWhite} />
            <h5 className={classNames(classes.textWhite, classes.ml3)}>
              {months}{" "}
              {months === 1
                ? translate("challenge.month")
                : months < 5
                ? translate("challenge.months")
                : translate("challenge.month_gen")}
            </h5>
          </div>
          <div
            className={classNames(
              classes.dFlex,
              classes.alignItemsCenter,
              classes.mt2
            )}
          >
            <FlagIcon size={25} className={classes.textWhite} />
            <h5 className={classNames(classes.textWhite, classes.ml3)}>
              {moment(props.start).format("DD.MM")} -{" "}
              {moment(props.end).format("DD.MM")}
            </h5>
          </div>
          <div
            className={classNames(
              classes.dFlex,
              classes.w100,
              classes.justifyContentBetween,
              classes.mt7
            )}
          >
            <div className={classNames(classes.dFlex, classes.flexCol)}>
              <div
                className={classNames(
                  classes.textGray100,
                  classes.textS,
                  classes.textMono
                )}
              >
                {translate("challenge.difficulty")}
              </div>
              <div
                className={classNames(
                  classes.dFlex,
                  classes.alignItemsCenter,
                  classes.mt2
                )}
              >
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 1 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 2 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 3 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 4 && classes.dotFilled
                  )}
                ></div>
                <div
                  className={classNames(
                    classes.difficultyDot,
                    difficulty >= 5 && classes.dotFilled
                  )}
                ></div>
              </div>
            </div>

            <img
              src={map}
              className={classNames(classes.challengeMap, classes.ml5)}
              alt=""
            />
          </div>
        </div>
        {props.actionBtn !== false && (
          <Button
            className={classNames(classes.challengeBtn)}
            fullwidth
            arrow
            onClick={props.onClick}
          >
            {translate("landing.showChallenge")}
          </Button>
        )}
      </div>
    </div>
  );
}

ChallengeCard.propTypes = {
  className: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string,
  start: PropTypes.string,
  difficulty: PropTypes.number,
};

export default compose(
  withStyles(styles),
  withLocalization,
  withStore,
  withDoby
)(ChallengeCard);
