import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import compose from "../../compose";
import { withRouter } from "react-router";
import { Card } from "@material-ui/core";
import withDoby from "../../consumers/withDoby";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  singleStep: {
    width: "25%",
    position: "relative",
    maxWidth: "96px",
    [theme.breakpoints.up("md")]: {
      maxWidth: "120px",
    },
  },
  stepperSpacing: {
    flexGrow: "1",
    borderBottom: "4px solid #E4EAF3",
    [theme.breakpoints.up("md")]: {
      borderBottom: "5px solid #E4EAF3",
    },
  },
  stepperSpacingStart: {
    borderColor: "#00263E",
  },
  stepTitle: {
    textAlign: "center",
    fontSize: "11px",
    marginBottom: "6px",
    fontWeight: "500",
  },
  filledDot: {
    fill: "#00263E",
  },
  strokeDot: {
    fill: "#C8D2E1",
  },
  strokeDotActive: {
    fill: "#00263E",
  },
  dashedLine: {
    stroke: "#C8D2E1",
  },
  dashedLineActive: {
    stroke: "#47556A",
  },
  stepImg: {
    fill: "#E4EAF3",
  },
  stepImgActive: {
    fill: "#00263E",
  },
});

function checkoutStepper(props) {
  const { classes } = props;
  let curState = props.currentState;

  const getChallangeId = () => {
    const { data } = props.doby.cartGetProducts();
    const challenge = data.products.find(
      (x) => x.product.catalog_id === "pack"
    );
    return challenge ? challenge.product.challengeRef.id : null;
  };

  const changeStep = (step) => {
    const challenge = getChallangeId();
    switch (step) {
      case 0: {
        if (!challenge) return;

        props.history.push(`/challenge-checkout-package/${challenge}`);
        break;
      }

      case 1: {
        if (!challenge) {
          props.history.push(`/eshop`);
          return;
        }

        props.history.push(`/challenge-checkout-equipment/${challenge}`);
        break;
      }

      case 2: {
        props.history.push(`/eshop/checkout-delivery-payment`);
        break;
      }

      case 3: {
        props.history.push(`/eshop/checkout-summary`);
        break;
      }
    }
  };

  return (
    <>
      <div
        className={classNames(
          classes.w100,
          classes.dFlex,
          classes.justifyContentCenter,
          props.className
        )}
      >
        <div
          className={classNames(
            classes.stepperSpacing,
            classes.stepperSpacingStart
          )}
        ></div>
        <div
          className={classNames(
            classes.singleStep,
            classes.dFlex,
            classes.flexCol,
            classes.justifyContentEnd
          )}
          onClick={() => changeStep(0)}
        >
          <div className={classNames(classes.stepTitle, classes.textMono)}>
            {props.translate("stepper.starterpack")}
          </div>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fillRule="evenodd"
            strokeLinejoin="bevel"
            strokeMiterlimit="1.5"
            clipRule="evenodd"
            viewBox="0 0 100 50"
          >
            <path
              fill="#00263E"
              fillRule="nonzero"
              d="M12 6A6 6 0 110 6a6 6 0 0112 0z"
              transform="translate(-135.58 -288.434) translate(182.098 243.34) translate(-2.518 45.094)"
              className={classNames(classes.filledDot)}
            ></path>
            <path
              fill="none"
              stroke="#00263E"
              strokeDasharray="2.04,3.06,0,0"
              strokeWidth="1.02"
              d="M203.892 161.587V197"
              transform="translate(-135.58 -288.434) translate(182.098 243.34) matrix(1 0 0 .95833 -200.41 -94.825)"
              className={classNames(classes.dashedLineActive)}
            ></path>
            <path
              fill="#C4C4C4"
              fillRule="nonzero"
              d="M0 19.418v4.267h100V16l-2.4.218-4.8 1.067h-9.067l-6.4 1.066-17.6 1.067H0z"
              transform="translate(-135.58 -288.434) translate(182.098 243.34) translate(-46.518 71.453)"
              className={classNames(classes.stepImgActive)}
            ></path>
          </svg>
        </div>
        <div
          className={classNames(
            classes.singleStep,
            classes.dFlex,
            classes.flexCol,
            classes.justifyContentEnd
          )}
          onClick={() => changeStep(1)}
        >
          <div className={classNames(classes.stepTitle, classes.textMono)}>
            {props.translate("stepper.equipment")}
          </div>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fillRule="evenodd"
            strokeLinejoin="bevel"
            strokeMiterlimit="1.5"
            clipRule="evenodd"
            viewBox="0 0 100 50"
          >
            {curState > 2 && (
              <path
                fill="#00263E"
                fillRule="nonzero"
                d="M12 6A6 6 0 110 6a6 6 0 0112 0z"
                transform="translate(-254.879 -294.438) translate(182.098 243.34) translate(116.781 51.098)"
                className={classNames(classes.filledDot)}
              ></path>
            )}
            {curState < 3 && (
              <path
                fill="#47A7E3"
                d="M12 6A6 6 0 110 6a6 6 0 0112 0zm-2 0a4 4 0 11-8 0 4 4 0 018 0z"
                transform="translate(-254.879 -294.438) translate(182.098 243.34) translate(116.781 51.098)"
                className={classNames(
                  curState > 1 ? classes.strokeDotActive : classes.strokeDot
                )}
              ></path>
            )}
            <path
              fill="none"
              stroke="#00263E"
              strokeDasharray="2.04,3.06,0,0"
              strokeWidth="1.02"
              d="M203.892 161.587V197"
              transform="translate(-254.879 -294.438) translate(182.098 243.34) matrix(1 0 0 .95833 -81.111 -88.821)"
              className={classNames(
                curState > 1 ? classes.dashedLineActive : classes.dashedLine
              )}
            ></path>
            <path
              fill="#C4C4C4"
              fillRule="nonzero"
              d="M100 23.85v-7.685l3.467-.315 6.4-1.067 3.733 1.067 3.2-1.067 2.133-1.067 2.667-1.067 1.6 1.6 3.733 1.601 3.883-.853 2.904-1.721 2.475-2.528 1.774-2.899 1.937 2.28 1.961 1.454 3.552-.166 1.99-1.53 1.049-1.64 1.64-1.372 1.369 3.641 1.6 2.133 5.866-2.133 3.734-2.667 1.066-2.133 1.102-1.584 1.425-.645 1.207 2.229 1.119 2.133 1.987 1.867 2.077-1.183 1.231-1.559 1.394 1.149 1.559 5.279 1.45-2.079.93-1.874 1.695 1.6 1.997-2.434 1.067-1.832.957 1.019.766 3.001 1.641-2.188 1.367-2.653 2.051 1.37 2.052-2.409.902-1.805 1.094-1.997 1.149 1.477 1.367.889 1.176-1.764L200 0v23.85H100z"
              transform="translate(-254.879 -294.438) translate(182.098 243.34) translate(-27.22 77.292)"
              className={classNames(
                curState > 1 ? classes.stepImgActive : classes.stepImg
              )}
            ></path>
          </svg>
        </div>

        <div
          className={classNames(
            classes.singleStep,
            classes.dFlex,
            classes.flexCol,
            classes.justifyContentEnd
          )}
          onClick={() => changeStep(2)}
        >
          <div className={classNames(classes.stepTitle, classes.textMono)}>
            {props.translate("stepper.delivery")}
          </div>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fillRule="evenodd"
            strokeLinejoin="bevel"
            strokeMiterlimit="1.5"
            clipRule="evenodd"
            viewBox="0 0 100 50"
          >
            {curState > 3 && (
              <path
                fill="#00263E"
                fillRule="nonzero"
                d="M12 6A6 6 0 110 6a6 6 0 0112 0z"
                transform="translate(-373.749 -291.436) translate(182.098 243.34) translate(235.651 48.096)"
                className={classNames(classes.filledDot)}
              ></path>
            )}
            {curState < 4 && (
              <path
                fill="#47A7E3"
                d="M12 6A6 6 0 110 6a6 6 0 0112 0zm-2 0a4 4 0 11-8 0 4 4 0 018 0z"
                transform="translate(-373.749 -291.436) translate(182.098 243.34) translate(235.651 48.096)"
                className={classNames(
                  curState > 2 ? classes.strokeDotActive : classes.strokeDot
                )}
              ></path>
            )}
            <path
              fill="none"
              stroke="#00263E"
              strokeDasharray="2.04,3.06,0,0"
              strokeWidth="1.02"
              d="M203.892 161.587V197"
              transform="translate(-373.749 -291.436) translate(182.098 243.34) matrix(1 0 0 .95833 37.759 -91.823)"
              className={classNames(
                curState > 2 ? classes.dashedLineActive : classes.dashedLine
              )}
            ></path>
            <path
              fill="#C4C4C4"
              fillRule="nonzero"
              d="M200 23.85V0l1.067 7.316 2.461-2.721 1.272 2.721 1.244 2.612 1.423.588 1.067-2.133 2.666-2.134 2.133-2.666 2.134 2.666 1.9-1.066.875 1.066 1.313-.834.793-.957 2.161 1.791 1.559-.533 1.203.711.739 1.422h1.724l1.339-1.6 1.614-.341 1.367-1.614.766 1.614.915 2.215 4.582 1.805 2.516.547 1.558-2.133 1.641-1.6 1.723-1.258 1.832.724 1.779-2.666 2.134 3.2 2.133 3.733 3.2 1.067 2.133-2.667 1.6-2.133 3.734 3.733 10.9 2.174 4.267 2.134 9.6 2.133 10.933.81v6.124H200z"
              transform="translate(-373.749 -291.436) translate(182.098 243.34) translate(-8.35 74.29)"
              className={classNames(
                curState > 2 ? classes.stepImgActive : classes.stepImg
              )}
            ></path>
          </svg>
        </div>

        <div
          className={classNames(
            classes.singleStep,
            classes.dFlex,
            classes.flexCol,
            classes.justifyContentEnd
          )}
          onClick={() => changeStep(3)}
        >
          <div className={classNames(classes.stepTitle, classes.textMono)}>
            {props.translate("stepper.summary")}
          </div>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fillRule="evenodd"
            strokeLinejoin="bevel"
            strokeMiterlimit="1.5"
            clipRule="evenodd"
            viewBox="0 0 100 50"
          >
            <path
              fill="#47A7E3"
              d="M12 6A6 6 0 110 6a6 6 0 0112 0zm-2 0a4 4 0 11-8 0 4 4 0 018 0z"
              transform="translate(-488.33 -271.279) translate(182.098 243.34) translate(350.232 27.94)"
              className={classNames(
                curState > 3 ? classes.strokeDotActive : classes.strokeDot
              )}
            ></path>
            <path
              fill="none"
              stroke="#00263E"
              strokeDasharray="2.04,3.06,0,0"
              strokeWidth="1.02"
              d="M203.892 161.587V197"
              transform="translate(-488.33 -271.279) translate(182.098 243.34) matrix(1 0 0 .95833 152.34 -111.98)"
              className={classNames(
                curState > 3 ? classes.dashedLineActive : classes.dashedLine
              )}
            ></path>
            <path
              fill="#C4C4C4"
              fillRule="nonzero"
              d="M339.733 19.857l-21.866-1.067-14.4-.533L300 18v6.124h100v-4.267h-60.267z"
              transform="translate(-488.33 -271.279) translate(182.098 243.34) translate(6.232 53.86)"
              className={classNames(
                curState > 3 ? classes.stepImgActive : classes.stepImg
              )}
            ></path>
          </svg>
        </div>

        <div className={classNames(classes.stepperSpacing)}></div>
      </div>
    </>
  );
}

export default compose(
  withStyles(styles),
  withLocalization,
  withRouter,
  withDoby
)(checkoutStepper);
