import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Button from "../dobyComponents/form/Button";
import compose from "../../compose";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import getSharedStyles from "../../style";

const styles = (theme) => ({
    ...getSharedStyles(theme),
    profileImgHolder: {
        width: "24px",
        height: "24px",
        minWidth: "24px",
        borderRadius: "50px",
        overflow: "hidden",
        position: "relative",
    },
    profileImg: {
        width: "100%",
        height: "100%",
        position: "absolute",
        top: "0",
        left: "0",
        objectFit: "cover",
    },
    postImgWrapper: {
        width: "100%",
        height: "220px",
        background: "rgba(0,0,0,0.1)",
        overflow: "hidden",
        position: "relative",
    },
    postImg: {
        width: "100%",
        height: "100%",
        position: "absolute",
        left: 0,
        right: 0,
        objectFit: "cover",
    },
    postCard: {
        background: "#ffffff",
    },
    notifiCard: {
        borderBottom: '1px solid rgba(0, 0, 0, 0.1)'
    }
});

const convertDate = (date) => {
    return `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()}`;
};

function NotificationCard(props) {
    const { classes, translate } = props;

    const created = new Date(props.created);
    return (
        <div className={classNames(props.className, classes.bgWhite, classes.py5, classes.notifiCard)}>
            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.justifyContentBetween)}>
                <div className={classNames(classes.dFlex, classes.flexCol)}>
                    <div className={classNames(classes.dFlex)}>
                        {/* AVATAR */}
                        <div className={classNames(classes.profileImgHolder)}>
                            <img
                                alt="avatarImg"
                                src={props.avatar ? "data:image/jpeg;base64," + props.avatar : "./img/user-img.jpg"}
                                className={classNames(classes.profileImg)}
                            />
                        </div>

                        {/* TEXT */}
                        <div className={classNames(classes.textM, classes.ml2)}>{props.subject}</div>
                    </div>

                    {/* DATUM */}
                    <div className={classNames(classes.dFlex, classes.mt2)}>
                        <div className={classNames(classes.textS, classes.textMono, classes.textGray300)}>{convertDate(created)}</div>
                    </div>
                </div>

                <div className={classNames(classes.flexCol, classes.ml3)}>
                    <div className={classNames(classes.textXs)}>
                        <Button onClick={props.onClick} size="sm">
                            {translate("show")}
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    );
}

NotificationCard.propTypes = {
    className: PropTypes.object.isRequired,
    avatar: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    subject: PropTypes.string.isRequired,
    created: PropTypes.string.isRequired,
};

export default compose(withStyles(styles), withLocalization)(NotificationCard);
